          p Debido al carcter multifactico del turismo, se puede encontrar diferentes definiciones que han sido aportadas desde varias disciplinas como la economa, historia, psicologa, ecologa, geografa, ciencias polticas, administrativas, entre otras; sin embargo,  una de las primeras definiciones aceptadas mundialmente ha sido la de la Organizacin Mundial del Turismo (OMT, 1994), quien menciona que el turismo comprende las actividades que realizan las personas durante sus viajes y estancias en lugares distintos a su entorno habitual, por un periodo de tiempo consecutivo inferior a un ao, con fines de ocio, por negocios y otros.
        p En el 2007, esta misma organizacin replante la definicin agregando: El turismo es un fenmeno social, cultural y econmico relacionado con el movimiento de las personas a lugares que se encuentran fuera de su lugar de residencia habitual por motivos personales o de negocios/profesionales.
      p El trmino hace entonces referencia al acto de visitar un destino por fuera del entorno habitual del sujeto e incluye la caracterstica movilidad y el carcter temporal de los viajes.
