        h2 1.2 Elementos bsicos o factores que componen la actividad turstica
    p.mb-5 El turismo, ms que una simple actividad, consiste en viajar a un lugar diferente al del entorno habitual, se considera que este viaje debe durar mnimo 24 horas y mximo 365 das.  Entre los diferentes elementos o factores que le corresponden se encuentran:
    .row.justify-content-center.fondo47
