          .tarjeta.fondo4.p-3(x="60.5%" y="13.3%" numero="+")
            .titulo-sexto.color-primario
              p.text-bold.mb-0 1. LA DEMANDA
            span.mt-4 Formada por el conjunto de consumidores de bienes y servicios tursticos que buscan satisfacer sus necesidades de viaje. Segn la OMT, es el conjunto de turistas que, de forma individual o colectiva, estn motivados por una serie de productos o servicios tursticos con el objetivo de cubrir sus necesidades.

          .tarjeta.fondo4.p-3(x="60.5%" y="36%" numero="+")
            .titulo-sexto.color-primario
              p.text-bold.mb-0 2. LA OFERTA
            span.mt-4 Son los productos y servicios que un destino pone a disposicin del turista para que ste los adquiera al precio interno y por un periodo de tiempo determinado.

          .tarjeta.fondo4.p-3(x="60.5%" y="63.3%" numero="+")
            .titulo-sexto.color-primario
              p.text-bold.mb-0 3. EL ESPACIO GEOGRFICO
            span.mt-4 Lugar donde se produce la interaccin entre la oferta y la demanda, es decir donde se realiza la prctica de actividades tursticas. La planta turstica y los atractivos tursticos son los elementos primordiales del espacio turstico.

          .tarjeta.fondo4.p-3(x="60.5%" y="89.1%" numero="+")
            .titulo-sexto.color-primario
              p.text-bold.mb-0 4. LOS OPERADORES DEL MERCADO
            span.mt-4 Son aquellas empresas y organismos que facilitan la correcta relacin entre la oferta y la demanda, como ejemplo de ellos se pueden nombrar: organismos pblicos y privados dedicados a la regulacin y/o a la promocin del turismo, compaas de transporte y agencias de viajes.

    p.mt-5(data-aos="fade-up-left") Segn la OMT (1998) los operadores tursticos son aquellos agentes que participan en la actividad turstica en calidad de intermediarios entre el consumidor final (el turista, la demanda turstica) y el producto turstico (bien o servicio, destino turstico) aunque puede extender su accin intermediadora al resto de la oferta complementaria (restaurantes, hoteles, conjunto de oferta de alojamiento, etc.)".
