        p.mb-0 Son todas las empresas que tienen contacto con los viajeros para proporcionarles diferentes bienes y servicios tursticos dentro de un territorio con atractivo especial. Para el caso de Colombia, todos deben contar con el Registro Nacional de Turismo y en lo posible cumplir con los requisitos de sostenibilidad del destino.

    p.mt-5(data-aos="fade-up-left") Los prestadores de servicios tursticos deben estar pendientes de que clientes o proveedores acaten las normas de operacin local; adicionalmente deben asegurar la capacitacin del personal de trabajo y hacer cumplir los principios de la actividad turstica por parte de todos los visitantes. 
    p.mb-5(data-aos="fade-up-left") Los prestadores de este tipo de servicios, tiene funciones especficas para con los diferentes actores del proceso, entre las que se encuentran:

