                h4 2. Transporte
                p Es aquel servicio de alquiler de vehculos, con o sin conductor, que se facilita como parte de la actividad turstica y que se presta bajo la responsabilidad de una empresa legalmente constituida para tales fines y debidamente habilitada en esta modalidad.
