                h4 4. Agencias de viajes y turismo (vts, operadoras y mayoristas)
                p Agencias de viajes y turismo (vts, operadoras y mayoristas): son las empresas que se dedican profesionalmente al ejercicio de actividades tursticas dirigidas a la prestacin de servicios, directamente o como intermediarios entre los viajeros y proveedores de los servicios.
