      h2 1.4 Turista y sus motivaciones

    p(data-aos="fade-up-left") Cualquier persona que se encuentre realizando un viaje (por cualquier motivo) entre lugares geogrficamente distintos y fuera de su entorno habitual, se puede llamar viajero; sin embargo, cuando una persona se establece en otro lugar para trabajar, o porque son refugiados, diplomticos o migrantes, no sern considerados viajeros.
    p(data-aos="fade-up-left") Segn esta definicin y teniendo en cuenta que visitante es toda persona que se desplaza a una localidad donde no tiene fijada su residencia, Gurra (2004) categoriza estos viajeros en dos tipos (ver figura 1):
    .titulo-sexto.color-acento-contenido(data-aos="fade-up-left")
      span.text-bold.mb-0 Figura 1 
      span Diferencias entre turista y excursionista.
    .row.mb-5.justify-content-center.align-items-center
      .col-xxl-4.col-xxl.pe-0.col-xxl.me-3.col-xxl.pb-4.col-xl-5.col-lg-6.col-md-10.col-sm-10.col-12.col-lg.mb-5.mb-lg-0(data-aos="fade-up-left")
