    p(data-aos="fade-up-left") Respecto a la satisfaccin de necesidades, se ha hablado mucho en la literatura, sin embargo, el autor que ha sido ms reconocido en el tema, ha sido Maslow, quien brind una teora psicolgica a partir de la cual se dise la jerarqua de las necesidades humanas que incluye las de seguridad (proteccin, seguridad o refugio), las fisiolgicas (hambre, descanso o sed), las necesidades de estima (autoestima, estatus o reconocimiento por los dems), las necesidades sociales (de amor o pertenencia), y las necesidades de autorrealizacin (de realizacin y desarrollo personal).  
    p(data-aos="fade-up-left") As pues, se puede decir que todos los viajes pueden tener un objetivo o finalidad diferente; habr quienes busquen recreacin, o quienes lo hagan por motivos profesionales o bien por otras razones como estudio, salud entre otros (ver tabla1).  
