    p.my-5.text-center(data-aos="fade-up") Nota. Adaptado de Gutirrez y Bordas (1993) por Cordente Rodrguez, M; Mondjar Jimnez, J.A; Esteban Talaya, A (s.f.).
    p.mt-5(data-aos="fade-up") Segn la OMT, el perfil del turista ha ido evolucionando durante los ltimos aos. Hoy por hoy, se puede encontrar un viajero mucho ms exigente, informado, digital y comprometido con el medio ambiente y el entorno. 
    p.mb-5(data-aos="fade-up") A pesar de que se siguen presentando las motivaciones descritas en la seccin anterior, tambin se han venido presentando otras como la prctica de negocios, actividades saludables, formacin o aprendizaje; actividades de sanacin o espiritualidad y enriquecimiento cultural; todas ellas han dado lugar a nuevas tipologas de turista como puede apreciarse en el siguiente recurso educativo, se puede ver:

    //-slider    
    .tarjeta.tarjeta--azul.p-4.mb-5.fondo7(data-aos="fade-up-right")
      SlyderA.pt-4(tipo="b")
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 El turista gastronmico
            p Tambin llamado <i>foodie</i>, su viaje se basa en su pasin por la gastronoma. Les gusta conocer bares, <i>pubs</i> o restaurantes, degustando la comida local del pas.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/gastronomico.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 El turista cultural
            p Visita lugares donde la cultura y el patrimonio histrico-artstico son destacables. Son turistas con alta capacidad de gasto y altos niveles de educacin que son capaces de estimular la actividad cultural de un destino turstico.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/cultural.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 El turista de negocios y los nmadas digitales 
            p El turista de negocios es impulsado por su trabajo para viajar por negocios y reuniones. Los nmadas digitales trabajan como freelancers y encuentran en los viajes una fuente de inspiracin para poder trabajar. Este colectivo dispone de cortos periodos de tiempo para conocer nuevos destinos.

          .col-md-7
            figure
              img(src='@/assets/curso/tema1/nomada.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 El turista comprador
            p La principal motivacin para viajar es adquirir productos por fuera de su pas de origen. En el <i>"shopping"</i> se prioriza la calidad tanto en los comercios donde se realizan las compras como en el tiempo que se dedica a las mismas.
            p Este tipo de turistas combina sus viajes con el turismo cultural conociendo as los lugares ms importantes del destino, as como sus mejores lugares para comprar.
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/comprador.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 El turista aventurero
            p Es el interesado por las nuevas sensaciones; cuenta con un gran sentido de libertad que lo mueve a conocer el mundo sin mucha planificacin.
            p Busca recreacin activa para su entretenimiento, por ello normalmente se le asocia con actividades al aire libre.
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/aventurero.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Mascotas con dueo
            p Dentro de los nuevos perfiles de viajeros ocupa un lugar cada vez ms importante el de los turistas que viajan con mascotas, o ms bien, las mascotas que viajan con sus dueos, ya que es el animal de compaa el verdadero protagonista del viaje y el que condiciona el destino, el alojamiento elegido y el transporte.
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/mascotas.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 <i>PANK</i>: tas con sobrinos
            p Las <i>PANK (Professional Aunt, No Kids)</i> son mujeres que no tienen hijos pero que se llevan de viaje a sus sobrinos. 
            p Es una tendencia al alza que ya se est convirtiendo en todo un nicho de mercado, sobre todo en EEUU y Europa.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/pank.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Viajeros solos
            p Con edades entre los 35 y los 60 en busca de destinos poco convencionales durante cualquier poca del ao.
            p Ese es el perfil predominante del viajero single, un nuevo tipo de turista que apuesta por descubrir el mundo en solitario.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/solos.png')
    separador
    #evolucion.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.5 Evolucin del turismo
    p.mb-5(data-aos="fade-up-left") Para el caso colombiano, el turismo comenz a desarrollarse en mayor medida con la llegada del tren a comienzos del siglo XX, lo que permiti que varias familias pudieran realizar viajes cortos de descanso y diversin a los destinos cercanos a sus hogares. Lo anterior tambin impuls la organizacin de <i>tours</i>, ya que algunas personas comenzaron a ofrecer viajes incluyendo los pasajes de tren, alimentacin, alojamientos en establecimientos como fincas y hoteles, y visitas a monumentos y recintos religiosos. Se podr entender un poco ms sobre la evolucin del turismo con la siguiente lnea del tiempo:
    .row.justify-content-center(data-aos="fade-up-right")
      .col-xxl-10.col-lg-12.col-md-12.col-12.fondo9
        LineaTiempoB.my-5.px-4(:datos="datosLineaTiempoB")    

    p.my-5(data-aos="fade-up-left") As surgieron las primeras agencias de viajes, que posteriormente a mediados de siglo, con la llegada de la aviacin al pas, crecieron en mayor medida, porque tuvieron la oportunidad de incursionar en el mbito internacional.
    .row.justify-content-around.align-items-center(data-aos="fade-up-left")
      .col-xxl-10.col-xl-10.col-11.col-md-11
        .tarjeta.fondo8.p-3.mb-5
          .row.justify-content-center.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/tema1/figura2.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p Se invita a conocer un poco ms sobre el turismo, revisando el siguiente artculo publicado por El Tiempo el 16 de octubre del 2008, que agrupa los 25 hitos de la historia del turismo colombiano, y que puede consultar en el siguiente enlace: 
                .col-sm-auto
                  a.boton.color-acento-botones.texto-blanco(:href="obtenerLink('downloads/25_hitos_de_la_historia_del_turismo_colombiano.pdf')" target="_blank")
                    span Descargar
                    i.fas.fa-file-download

    p.my-5(data-aos="fade-up-left") En cuanto a las tendencias que rigen el turismo, se encuentra que la FITUR (2021), que es la feria de turismo ms importante de Espaa y Procolombia (2021), indica que entre las preferencias del turismo en los aos entrantes estarn:
    .row.justify-content-center.align-items-center
      .col-xxl-6.col-lg-6.col-md-6.col-sm-10.col-11(data-aos="fade-up-left")
        LineaTiempoD.color-secundario
          p(numero="1" titulo="Ecoturismo y turismo sostenible") Un modo de hacer turismo que consiste en el respeto por el medio ambiente y el planeta, acorde con los principios de sostenibilidad y la conservacin de los recursos de los destinos tursticos.

          p(numero="2" titulo="<i>Mindful Travel</i>") Es un modo de viajar relacionado con el bienestar, autocuidado y la transformacin personal. Son viajes tranquilos donde no se cumple con horarios estrictos ya que su principal objetivo es que los turistas, realmente conecten con el destino, con su cultura, sus costumbres y tradiciones.

          p(numero="3" titulo="Experiencias tursticas") Los turistas buscan experiencias memorables, sentir emociones y vivir aventuras placenteras en cada uno de sus viajes. El viajero experto busca actividades distintas, destinos tursticos que ofrezcan atractivos y experiencias diferentes, lejos del turismo masivo y en busca de vivencias en lugares especiales.

          
          p(numero="4" titulo="Turismo de viajes nmadas") Son los empresarios y empleados que se desplazan hacia cualquier destino con la intencin de trabajar a distancia. Este tipo de turismo tambin combinado con los viajes, es una oportunidad excelente para que los destinos ofrezcan espacios ideales para trabajar y a la vez viajar. 
      .col-xxl-6.col-lg-6.col-md-6.col-sm-11.col-11(data-aos="fade-up-right")
        img.mt-4(src="@/assets/curso/tema1/imagen5.jpg")
    p.my-5(data-aos="fade-up-left") Tambin se puede encontrar nuevas preferencias de los turistas a la hora de viajar como:
    .row.justify-content-center.align-items-center
      .col-xxl-6.col-lg-6.col-md-6.col-sm-11.col-11(data-aos="fade-up-left")
        img.mb-4(src="@/assets/curso/tema1/imagen6.jpg")
      .col-xxl-6.col-lg-6.col-md-6.col-sm-10.col-11(data-aos="fade-up-right")
        ul.lista-ul--color
          li 
