            | Preferencia por destinos tursticos no masificados y viajes de una corta estancia en fechas de baja demanda.
          li
            i.fas.fa-check 
            p.mb-0 Bsqueda de alojamientos de pequeo tamao y sin espacios compartidos, como viviendas de turismo rural, apartamentos tursticos y <i>bungalow</i>.
          li 
