      SlyderA.pt-4.otroslide(tipo="b")
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo sostenible
            p El turismo que tiene plenamente en cuenta las repercusiones actuales y futuras, econmicas, sociales y medioambientales para satisfacer las necesidades de los visitantes, de la industria, del entorno y de las comunidades anfitrionas. 
            p Poco a poco la sociedad es cada vez ms consciente del impacto que sus acciones tienen en el entorno y el turismo no es una excepcin.
            p Muchas personas buscan que sus vacaciones no tengan repercusiones en el medio ambiente o al menos reducirlas al mximo.
        
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/sostenible.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo rural
            p Ofrece al turista la oportunidad de experimentar el encuentro con las diferentes formas de vivir de las comunidades que cohabitan en un ambiente rural y adems lo sensibiliza sobre el respeto y valor de su identidad cultural.
            p Este tipo de turismo se complementa con el agroturismo, que consiste en involucrar al turista, con las labores del campo y agrcolas, en donde se puede aprender tcnicas, cultura y vivir en el entorno natural de conservacin.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/rural.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo de aventura
            p Son los viajes que tienen como fin, realizar actividades recreativas, asociadas a desafos impuestos por la naturaleza.
            p En tierra, agua, aire, disfrutar de: ciclismo de montaa, alpinismo, <i>rappel</i>, espelesmo, cabalgata, descenso ros, pesca recreativa, <i>rafting</i> / kayakismo, vuelo en parapente, vuelo en ala delta y <i>bungee jumping</i>.

          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/aventura.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo gastronmico
            p Es un tipo de turismo emergente que atrae a viajeros que no solo buscan comer bien, sino descubrir que hay detrs de la gastronoma de un destino, vivir experiencias culinarias, recorrer la zona con rutas de vinos y tapas y visitar mercados en los que descubrir productos autctonos.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/gastro.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo de negocios
            p Los viajes de negocios, MICE (por sus siglas en ingles,<i> Meetings, Incentives, Conventions and Exhibitions</i>), se refiere a los traslados de una ciudad o pas a otra, relacionados con actividades corporativas o laborales. Este puede ser individual o grupal.
            ul.lista-ul--color
              li 
                i.fas.fa-caret-right
                | Es utilizado por empresarios, ejecutivos, comerciales y otros profesionales para cerrar negocios, captar clientes o prestar servicios.
              li
                i.fas.fa-caret-right
                | El cliente suele ser de alto poder adquisitivo.
              li 
                i.fas.fa-caret-right
                | Turismo fundamentalmente urbano
              li 
                i.fas.fa-caret-right
                | Se destacan las reuniones, congresos, convenciones, viajes de incentivos.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/negocios.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo de bienestar
            p Este incremento responde, sobre todo, a un cambio de mentalidad en la sociedad. Las personas estn ms implicadas en cuestiones relacionadas con su bienestar fsico y mental. De ah que cada vez surjan ms ofertas de viajes que incluyen retiros de yoga, una alimentacin saludable, tratamientos <i>wellness</i> y dems actividades relacionadas con el turismo de bienestar.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/bienestar.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo sol y playa
            p Aunque generalmente es masivo, de alto consumo y tiene un impacto considerable sobre los ecosistemas y la cultura anfitriona; hoy por hoy este tipo de turismo se viene combinando con la sostenibilidad, iniciativas de cuidados del medio ambiente y los viajes de bienestar, con la intencin de disminuir los impactos adversos y cuidar de los ecosistemas.

          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/solyplaya.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 El turismo urbano
            p Son los viajes realizados a las grandes ciudades y centros urbanos del mundo. El turista busca grandes aeropuertos con vuelos directos a cualquier parte del mundo, amplia infraestructura hotelera (adaptada a todas las necesidades) y una gran oferta gastronmica y actividades de ocio. El turismo urbano engloba a su vez relaciones de negocios, turismo educativo y turismo cultural puesto que las ciudades aglutinan una variada y robusta oferta de ocio y entretenimiento.
        
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/noche.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo mdico/salud
            p Es aquel en el que el turista se moviliza con la finalidad de realizar una consulta mdica o someterse a un proceso quirrgico al que no tiene acceso en su lugar de origen. Dentro de este grupo, la ciruga esttica es una de las ms demandadas, aunque puede tratarse de cualquier procedimiento o consulta.
        
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/salud.png') 
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Turismo educativo
            p El turista viaja para aprender dentro de un mbito acadmico. Por lo tanto, este tipo de turismo incluye viajes de intercambio, cursos de verano o visitas guiadas a laboratorios o instituciones.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/educativo.png') 

