    #leyes.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.7 Leyes en turismo
    p(data-aos="fade-up-left") El turismo no est exento de la aplicacin de leyes y normativas que regulan la industria de este sector, desde 1996 se ha ido reconociendo no slo su importancia, sino tambin la reglamentacin que favorece el servicio, y el rengln econmico al que pertenece y que genera un importante aporte a la economa del pas. Las principales leyes que enmarcan la industria del turismo son (ver tabla 2):

