        tr
          th.bordetabla
            h4.text-center Objeto de la ley
          td.alinearArriba.bordetabla 
            p Ley general del turismo, reconoce la importancia del turismo para el desarrollo nacional.
          td.alinearArriba.bordetabla
            p Modifica la ley 300 de 1996 y crea el impuesto nacional con destino al turismo como inversin social mediante la promocin y el fortalecimiento de la competitividad que comprende la capacitacin y la calidad tursticas.
          td.alinearArriba.bordetabla
            p Fomenta, el desarrollo, la promocin, la competitividad del sector y la regulacin de la actividad turstica, mediante el aprovechamiento de los recursos y atractivos tursticos nacionales.
          td.alinearArriba.bordetabla
            p Fomenta la sostenibilidad e implementa mecanismos para la conservacin, proteccin y aprovechamiento de los destinos y atractivos tursticos, formaliza la competitividad del sector y promueve la recuperacin de la industria turstica, mediante incentivos, la calidad y el impulso de oportunidades del sector.
