    p.mt-5(data-aos="fade-up-left") La ltima ley generada para regular las actividades del sector es la 2060 de 2020, esta ley modifica el programa de apoyo al empleo formal PAEF y el programa de apoyo para el pago de la prima de servicios PAP.
    p.mb-5(data-aos="fade-up-left") Entre sus objetivos principales est fortalecer la sostenibilidad, la formalizacin y la competitividad del sector y promover la reactivacin del turismo, para consolidar el pas como un destino altamente reconocido, sostenible, responsable y de alta calidad. Adems de tener como propsito fundamental, reactivar econmicamente al sector despus de un ao de pandemia, combinando medidas de corto, mediano y largo plazo.
