    #sistema.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.8 Sistema turstico
    .row.justify-content-center
      .col-xxl-6.col-xl-6.col-lg-6.col-md-10.col-sm-10.col-11(data-aos="fade-up-left")
        .cajon.puntacajon.p-4.mb-4.fondo17.sombracajon
          p Todo sistema es un conjunto de partes jerarquizadas que se interrelacionan y se coordinan entre s permitiendo alcanzar una determinada funcin o comportamiento. El turismo a su vez es un conjunto de elementos conectados entre s que generan repercusiones positivas o negativas en un territorio concreto. Todos esos elementos conforman un sistema donde las partes estn conectadas. No existe una sola forma de explicar el sistema turstico ya que han sido diferentes los autores que han abordado el tema, sin embargo, todos ellos concuerdan en que el sistema turstico tiene varias facetas y a su vez, el estudio de cada una de ellas ha permitido crear varios modelos que buscan analizar su funcionamiento.
        p Una de las teoras ms utilizadas es la de Roberto Boulln propuesta en el 2004, quien caracteriza esas facetas del sistema turstico teniendo en cuenta la oferta y la demanda, tal como puede observarse en la siguiente figura 5:        
      .col-xxl-6.col-xl-6.col-lg-6.col-md-6.col-sm-11.col-11(data-aos="fade-up-right")
        img(src='@/assets/curso/tema1/imagen7.jpg', alt='Sistema turstico')

    .titulo-sexto.color-acento-contenido.mt-5(data-aos="fade-up-left")
      span.text-bold.mb-0 Figura 5 
      span Funcionamiento del sistema turstico segn Roberto Boulln
    .row.justify-content-center.fondo16.ampliarambos
      .col-xxl-12.px-5
        .row.justify-content-center(data-aos="fade-up-left")
          .col-xxl-6.col-xl-6.col-lg-7.col-md-10.col-sm-11.col-12
            img(src='@/assets/curso/tema1/sistematuristico.svg', alt='Sistema turstico segn Boulln')
        p.mt-5.text-center(data-aos="fade-up-left") Nota. Planificacin del espacio turstico. Boulln, R (2004). 
    p.my-5(data-aos="fade-up-left") Tal como lo explica Boulln, en la grfica se observa que en la parte izquierda, el punto de partida del funcionamiento del sistema, surge del encuentro entre oferta y demanda, mediante un proceso de venta llamado producto turstico. Este  junto con la infraestructura constituyen el centro de produccin del sector, tal como se aprecia en la parte derecha de la imagen. En el centro, se ha representado la estructura turstica, que tiene por funcin controlar la eficiencia del sistema, vigilando el funcionamiento e interrelacin de las partes; con el fin de dar mayor claridad a lo explicado, se ampliarn cada uno de estos conceptos:
    .row.ampliarambos
