                p.text-dark Es el conjunto de prestaciones tangibles e intangibles (que incluyen aspectos involucrados en la infraestructura, estructura y superestructura turstica) las cuales se integran para ofrecer un producto que satisfaga las necesidades, deseos y expectativas del turista. Est conformado por tres elementos: los atractivos, el acceso y las facilidades.
