    p(data-aos="fade-up-left") Anteriormente, se menciona que la planta turstica est conformada por equipamiento e instalaciones. Es importante detenerse un poco en este punto. El equipamiento hace referencia a los lugares que le facilitan la estancia al turista y le permiten su estada como hoteles, restaurantes, lugares de esparcimiento y otros. Las instalaciones por su parte son las construcciones que le permiten al visitante la prctica de actividades tursticas. 
