    p.mb-5(data-aos="fade-up-left") En cuanto a la infraestructura turstica, se debe recordar que se trata de todas las obras y servicios que no solo apoyan al sector del turismo sino, tambin a los dems sectores para que las actividades econmicas de un destino se desarrollen con normalidad. Puede existir infraestructura netamente creada para el turismo como caminos, vas martimas, etc. Sin embargo, la infraestructura es una de las bases esenciales para el desarrollo de cualquier territorio. Los componentes de la infraestructura dedicada al turismo son:

