    p.my-5(data-aos="fade-up-left") Cuando se habla de la superestructura, se integra tanto por organismos pblicos como privados que tienen la misin de planificar, optimizar y regular el funcionamiento del sistema turstico, facilitando la produccin y venta de los servicios por los cuales est compuesto el producto turstico.
    .row.justify-content-center(data-aos="fade-up")
      .col-xxl-10.col-xl-10.col-lg-10
        .cajon.color-primario.p-4.mb-4.mt-4.fondo1
          p El sector pblico incluye entidades gubernamentales que contribuyen de manera directa o indirecta con la actividad; ejemplo de ellos son los Organismos Municipales de Turismo y las Secretaras de Cultura, Deportes o Produccin. El sector privado abarca a las instituciones y empresas que trabajan directa e indirectamente para el sector como Cmaras de turismo, hoteles, agencias de viajes, empresas de transportes, etc.

    .row.justify-content-center(data-aos="fade-down")
      .col-xxl-6.col-xl-6.col-lg-6.col-md-10.col-sm-10.col-11
        .cajon.puntacajon2.p-4.mb-4.fondo18.sombracajon
          p Por otra parte, los recursos y atractivos tursticos requieren ser diferenciados entre s. Los primeros son aquellos elementos naturales, culturales y humanos que contribuyen a generar el desplazamiento de los turistas (son potencialmente explotables por el turismo);  los segundos, son los recursos tursticos que cuentan con las condiciones necesarias para ser visitados y disfrutados por el turista, es decir, que cuentan con planta turstica, medios de transporte, servicios complementarios e infraestructura bsica (Boullon, 2006). Con esto se puede decir entonces, que no todos los recursos son atractivos tursticos.
        p Como se puede observar en el siguiente recurso educativo, la diferenciacin entre estos (recursos y atractivos tursticos), se encuentran en derredor e incorporados en el ambiente que rodea al turista:       
      .col-xxl-6.col-xl-6.col-lg-6.col-md-6.col-sm-11.col-11
