        p.text-dark Manifestaciones religiosas y creencias populares, ferias y mercados, msica y danzas, artesanas y artes populares, comidas y bebidas tpicas, grupos tnicos, arquitectura popular y espontnea.
      .tarjeta.otra.p-4.fondo19
        .row.justify-content-left.mb-3
          .col-xxl-4.col-lg-4.col-md-4.col-4
            img(src='@/assets/curso/tema1/slider4.png' alt='AvatarTop')
        h3.text-dark Realizaciones tcnicas, cientficas o artsticas contemporneas
        p.text-dark Explotaciones mineras, explotaciones agropecuarias, explotaciones industriales, obras de arte y tcnica, centros cientficos y tcnicos.
      .tarjeta.otra.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-4.col-lg-5.col-md-4.col-5
            img(src='@/assets/curso/tema1/slider5.svg' alt='AvatarTop')
        h3.text-dark Acontecimientos programados
        p.text-dark Artsticos, deportivos, ferias y exposiciones, concursos, fiestas religiosas y profanas y carnavales.
    p.my-5(data-aos="fade-up-left") Al respecto del Patrimonio turstico, este se refiere al conjunto de bienes tangibles e intangibles que se clasifican como naturales y culturales, y que funcionan como atractivos para el conocimiento y disfrute del turista; adems sirven como motores de motivacin para que los turistas se desplacen desde su lugar de residencia. El Patrimonio turstico est conformado por la infraestructura y la superestructura turstica, as como por la cantidad de activos tursticos, la planta y las instalaciones tursticas.
    separador
    #inventario.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.9 Inventario turstico

    .row.justify-content-center
      .col-xxl-6.col-xl-6.col-lg-6.col-md-6.col-sm-11.col-11(data-aos="fade-up-left")
        img.mt-4(src='@/assets/curso/tema1/imagen9.jpg', alt='Inventario turstico')
      .col-xxl-6.col-xl-6.col-lg-6.col-md-10.col-sm-10.col-11(data-aos="fade-up-right")
        .cajon.puntacajon.p-4.my-4.fondo17.sombracajon
          p Es un instrumento valioso que sirve para registrar ordenadamente los atractivos fsicos y culturales de un destino, a partir de sus caractersticas, su estado de conservacin y su clasificacin. El inventario le facilita el trabajo al agente de viajes, al planeador turstico o a los guas para poder determinar con bases claras cules atractivos se pueden incluir o no, en un producto turstico regional; de igual manera, este instrumento de registro sirve para realizar evaluaciones a dichos atractivos y as determinar las prioridades necesarias para el desarrollo turstico nacional. Tambin es importante aclarar que la informacin condensada en este inventario, debe ser actualizada permanentemente para que cumpla con las funciones dichas anteriormente.
        p Para dar inicio al inventario turstico es necesario llevar a cabo la clasificacin de los bienes y atractivos del lugar o lugares seleccionados. La evaluacin de los recursos y atractivos susceptibles de uso turstico que sern incluidos en el inventario, debe ser realizada objetivamente por un grupo interdisciplinario, formado por profesionales de varias ciencias del saber y determinado por el organismo pblico encargado del manejo del turismo en la regin o municipio.
    p.my-5(data-aos="fade-up-left") Los profesionales que participen de la creacin del inventario tendrn la potestad para clasificar los atractivos en dos grupos: patrimonio cultural y sitios naturales. 
    p.mb-5(data-aos="fade-up-left") En cuanto al patrimonio cultural, se entienden que son todas aquellas manifestaciones culturales, materiales e inmateriales que se encuentran en permanente construccin sobre el territorio transformado por las comunidades. El patrimonio cultural tiene una clasificacin propia, tal como se puede ver en el siguiente recurso:

    TabsC.color-primario(data-aos="fade-up")
      .py-3.py-md-4(titulo="Patrimonio material")
        .row
          .col-md-7.mb-4.mb-md-0
            p Se caracteriza por tener un cuerpo fsico que puede ser dimensionado y percibido sensorialmente
            p Esta constituido por el territorio geogrfico con sus recursos ambientales y naturales, los hechos construidos, los sistemas de transporte y las obras de infraestructura como vas, caminos  y puentes, entre otros; y todo el universo de herramientas, utensilios, mquinas y dems objetos que apoyan la vida productiva y cotidiana.

          .col-md-5
            figure
              img(src='@/assets/curso/tema1/imagen10.jpg')

      .py-3.py-md-4(titulo="Patrimonio inmaterial")
        .row
          .col-md-7
            p Comprende las tradiciones  y expresiones orales, prcticas sociales, rituales y actos festivos, el conocimiento y prcticas sobre la naturaleza y el universo; y expresiones artsticas, entre otras, que continuamente se estn recreando en la sociedad y reproduciendo generacionalmente. Es el <i>folklore</i>.

          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/tema1/imagen11.jpg')
        
      .py-3.py-md-4(titulo="Festividades y eventos")
        .row
          .col-md-7.mb-4.mb-md-0
            p Son atractivos que se generan en la realizacin de eventos con contenido actual o tradicional, en los cuales la poblacin es actora o espectadora.
        
          .col-md-5
            figure
              img(src='@/assets/curso/tema1/imagen12.jpg')

      .py-3.py-md-4(titulo="Grupos de especial inters")
        .row
          .col-md-7
            p Constituidos por comunidades indgenas, comunidades negras, y comunidades raizales, en las cuales se valora el respeto por sus costumbres como una forma de conservacin autntica de su legado cultural.
          .col-md-5.mb-4.mb-md-0
            figure
              img(src='@/assets/curso/tema1/imagen13.jpg')

    .row.justify-content-center
      .col-xxl-10.col-xl-10.col-lg-10(data-aos="fade-up")
        .cajon.color-primario.p-4.mb-4.mt-4.fondo1
          h4 Sitios naturales
          p Son aquellos que contemplan las reas geogrficas (conjunto de atractivos con sus componentes) y los recursos naturales (que por sus caractersticas no permiten estar agrupados) de importancia e inters para el turismo.
    separador
    #producto.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.10 Producto turstico
    p.mb-5(data-aos="fade-up-left") El turista generalmente compra la combinacin de paquetes tursticos que incluyen no solamente los atractivos de inters para l, sino tambin aquellas actividades que son complementarias en el pas o regin que pretende conocer; por tanto, cuando se habla de producto turstico, se puede definir como el conjunto de componentes tangibles e intangibles que incluyen recursos o atractivos tursticos, infraestructura, actividades, imgenes y valores simblicos para satisfacer motivaciones y expectativas, siendo percibidos en conjunto como parte de la experiencia turstica (Manual para la Planificacin de Productos Tursticos de Per-MINCETUR, 2014).
    .row.justify-content-between
      .col-xxl-3.col-xl-3.col-lg-3.col-md-6.fondo23.p-5(data-aos="fade-up-left")
        p El producto turstico se disea teniendo en cuenta las caractersticas de los diferentes nichos de mercado y la motivacin que stos presentan para viajar. De igual manera todo producto turstico debe brindar 3 tipos de beneficios como se puede ver a continuacin:
      .col-xxl-3.col-xl-3.col-lg-3.col-md-6.fondo22.pt-3(data-aos="fade-up")
        .row
          .col-xxl-4.col-xl-4.col-lg-5.col-md-4.col-sm-3.col-3
            img.mt-3.ms-3(src='@/assets/curso/tema1/nro1.svg')
          .col-xxl-12
            p.py-3.px-4 Los beneficios funcionales son aquellos que satisfacen necesidades elementales como alojamiento o comida, o aquellos que ofrecen ventajas en trminos de comodidad y facilidad (cercana, transporte cmodo y otros).
      .col-xxl-3.col-xl-3.col-lg-3.col-md-6.fondo22.pt-3(data-aos="fade-up")
        .row
          .col-xxl-4.col-xl-4.col-lg-5.col-md-4.col-sm-3.col-3
            img.mt-3.ms-3(src='@/assets/curso/tema1/nro2.svg')
          .col-xxl-12
            p.py-3.px-4 Los beneficios simblicos responden a emociones, status y realizacin personal. Ej. Utilizar servicios exclusivos, que el gua del <i>tour</i> o anfitrin sea una personalidad reconocida, visitar lugares que confieren status.
      .col-xxl-3.col-xl-3.col-lg-3.col-md-6.fondo22.pt-3(data-aos="fade-up")
        .row
          .col-xxl-4.col-xl-4.col-lg-5.col-md-4.col-sm-3.col-3
            img.mt-3.ms-3(src='@/assets/curso/tema1/nro3.svg')
          .col-xxl-12
            p.py-3.px-4 Los beneficios vivenciales estn relacionados con los cinco sentidos del ser humano, es decir, experiencias y vivencias resultantes de la actividad turstica. Ej. Ofrecer experiencias nicas, especiales, poco comunes, que la experiencia sea segura.
    p.mt-5(data-aos="fade-up-left") Cuando se piensa en crear productos tursticos, lo debe hacer de tal forma que sean novedosos, oportunos y eficaces para que aporten un valor econmico, social y ambiental, tanto para el destino como para los turistas quienes finalmente los disfrutarn.
    p.mb-5(data-aos="fade-up-left") En dicho sentido, los componentes necesarios para tal fin segn la Subdireccin de Productos y Destinos Sustentables Sernatur (2015) son:
    .row.justify-content-center
      .col-xxl-6.col-lg-6.col-md-11.col-sm-11.col-11(data-aos="fade-up-left")
        LineaTiempoD.color-secundario
          p(numero="1" titulo="Atractivo turstico") Es todo lugar, objeto o acontecimiento de inters turstico. Estos pueden ser atractivos naturales o culturales. Y pueden ser:<br/><br/><span style="font-weight: 700;"> Naturales: </span><span> aquellos asociados a la geografa y la ecologa, como los relacionados con las playas, el mar, la costa, el clima, el sol, la orografa, las montaas, los ros, la flora, la fauna.</span><br/><br/><span style="font-weight: 700;">Asociados al ocio: </span><span> instalaciones existentes para la prctica de diferentes tipos de deportes.</span><br/><br/><span style="font-weight: 700;">Recursos Culturales e histricos: </span><span> monumentos arqueolgicos, religiosos, museos, cascos antiguos, entre otros.</span>

          p(numero="2" titulo="Servicio turstico") Son todos los servicios que el turista requiere y consume mientras est de viaje como el alojamiento, alimentacin, transporte, agencias de viajes, etc.
          
          p(numero="3" titulo="Infraestructura turstica") Es la dotacin de bienes y servicios bsicos con que cuenta un pas, departamento o municipio y permite que el viaje sea cmodo y agradable, se puede clasificar en: red de transportes (red ferroviaria y vial), terminales terrestres (aeropuertos, terminales de buses, puertos, etc.), redes de servicios bsicos (agua potable, electricidad, etc.), redes de comunicaciones (correo, telfonos fijos, equipos celulares, fax, Internet, etc.).

          p(numero="4" titulo="Comunidad local") Habitantes, costumbres y tradiciones<br/><br/>El producto turstico debe incluir a los residentes, ya que son ellos los que ponen la diferencia principal entre un lugar y otro. En definitiva, es el sello que hace que un lugar sea agradable y nico para visitar, es lo que lo hace diferente.

          p(numero="5" titulo="Servicio turstico") Percepcin - Imagen - Emocin (Sensacin) <br/>independiente de las caractersticas que posea un destino concreto, no todos los turistas que lo visitan van a percibirlo de igual forma, ya que cada uno construir su propia imagen del destino y esto lo va a conectar con una emocin que finalmente se transforma en sensaciones.<br/><br/>Tambin debemos tener presente que normalmente un turista no se satisface a partir de la oferta de un solo proveedor. El transporte, la alimentacin, el alojamiento y las actividades sern entregadas por muchos oferentes distintos, razn por la que el xito de nuestro producto depende del xito de la combinacin de estos oferentes.

      .col-xxl-6.col-lg-6.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
        img.mt-4(src="@/assets/curso/tema1/imagen14.jpg")
    p.my-5(data-aos="fade-up-left") Una condicin ms a tener en cuenta corresponde con la fijacin de precios de los productos tursticos, y para hacerlo, se deben establecer los costos y gastos de la empresa o de quien est diseando el producto, en la siguiente tabla 4 se hace una conceptualizacin y diferenciacin entre los costos y los gastos de un producto turstico:

    .titulo-sexto.color-acento-contenido(data-aos="fade-up-left")
      span.text-small.mb-0.text-bold Tabla 4 
      span.text-small.mb-0 Concepto y diferencia entre costos y gastos de un producto turstico
    .row
      .col-xxl-12(data-aos="fade-up")
        <div class="table-responsive">
          <table class="table table-striped w-auto table-responsive">
            tr.fondo6
              th 
                h4.text-center.bordetablablanco GASTO
              td
                h4.text-center.bordetablablanco COSTO
              td
                h4.text-center DIFERENCIA ENTRE GASTO Y COSTO
            tr
              th.alinearArriba.bordetabla
                p Una partida que disminuye las ganancias (beneficio) de la empresa y que, por ende, contribuye a aumentar sus prdidas (deuda).
              td.alinearArriba.bordetabla 
                p Es el desembolso econmico que se realiza para la produccin de algn bien o la oferta de algn servicio. El costo incluye la compra de insumos, el pago de la mano de obra, los gastos en la produccin y los gastos administrativos, entre otras actividades.
              td.alinearArriba
                p El costo se define como todas aquellas inversiones necesarias para la produccin del bien o servicio, como son: la mano de obra o las materias primas. Los costos son considerados como inversiones, ya que se espera que vuelvan como forma de ganancias para la empresa. 
                p Los gastos son todos aquellos pagos que la empresa debe realizar por aspectos que no tienen que ver directamente con la produccin, como el pago de sueldos del personal administrativo. Estos gastos son los que permiten el correcto funcionamiento de la empresa, pero que no se encuentran directamente relacionados con las ganancias.
          </table>
        </div>
    p.text-center.my-4(data-aos="fade-up-left") Nota. Costo. Enciclopedia Concepto (2022).
    p.mb-5(data-aos="fade-up-left") En la siguiente grfica puede consultar con detalle cada uno de los costos y de los gastos que se precisan en la industria del turismo, y que son obligatorios para llevar a cabo una excelente prestacin del servicio:

    .row.fondo16.ampliarambos
      .col-xxl-12.col-lg-12.col-12.px-5(data-aos="fade-up")
        ImagenInfografica.color-secundario.mb-5
          template(v-slot:imagen)
            figure
              img(src="@/assets/curso/tema1/grafica1.svg")

          .tarjeta.fondo24.p-3(x="38.1%" y="28.6%" numero="+")
            p.text-bold.mb-0 Costos fijos
            span Son aquellos costos que posee una empresa ms all de la produccin obtenida, es decir, que su valor no vara segn lo producido. Ejemplos: el pago de alquileres y los impuestos.

          .tarjeta.fondo24.p-3(x="38.1%" y="36%" numero="+")
            p.text-bold.mb-0 Costos variables
            span Varan en relacin a lo producido, es decir, que si se aumenta la produccin estos costos sern mayores y viceversa. Ejemplos: la cantidad de materia prima utilizada, el embalaje y empaquetado de los productos.

          .tarjeta.fondo24.p-3(x="38.1%" y="51%" numero="+")
            p.text-bold.mb-0 Costos directos
            span Pueden ser asignados a cada bien o servicio en particular. Ejemplos: el aumento de harina (que tiene una repercusin directa sobre la produccin de pan) o el aumento de tinta (que impacta en la produccin de libros).

          .tarjeta.fondo24.p-3(x="38.1%" y="58.2%" numero="+")
            p.text-bold.mb-0 Costos indirectos
            span Tienen consecuencias sobre la produccin en forma total, es decir, que no pueden ser asignados a un determinado producto. Ejemplos: el aumento en insumos de limpieza de la fbrica o el aumento de los salarios de los trabajadores.

          .tarjeta.fondo24.p-3(x="38.1%" y="72.2%" numero="+")
            p.text-bold.mb-0 Costos de mano de obra
            span Derivan del pago de salarios de todos los miembros que participan de la estructura empresarial. 

          .tarjeta.fondo24.p-3(x="38.1%" y="79.2%" numero="+")
            p.text-bold.mb-0 Costos de materia prima
            span En los que se incurre al adquirir la materia prima para realizar un determinado producto o facilitar un servicio.

          .tarjeta.fondo24.p-3(x="38.1%" y="90%" numero="+")
            p.text-bold.mb-0 Costos financieros
            span Se necesitan para el financiamiento del negocio. 

          .tarjeta.fondo24.p-3(x="38.1%" y="97.6%" numero="+")
            p.text-bold.mb-0 Costos tributarios
            span Relacionados al pago de impuestos.

          .tarjeta.fondo24.p-3(x="75.7%" y="47.5%" numero="+")
            p.text-bold.mb-0 Gastos fijos
            span Independientemente del volumen de facturacin o la actividad del negocio, estos gastos no varan y deben asumirse sin excepcin. Algunos de estos gastos incluyen:
            br
            br
            span.text-bold.mb-0 Alquileres. 
            span Es uno de los gastos ms comunes que tienen las empresas, relacionado al pago de arrendamiento de oficinas o locales. Incluso, cuando el modelo de negocio reposa en la utilizacin de un espacio de <i>coworking</i>, este gasto es fijo y debe incluirse dentro del cdigo correspondiente.
            br
            br
            span.text-bold.mb-0 Nminas. 
            span Este tipo de gasto, adems de contemplar la nmina de sueldos, contempla igualmente lo relacionado con la Seguridad Social, ya sea que se trate de una empresa que cotiza al Rgimen General o de la Seguridad Social de Autnomos.
            br
            br
            span.text-bold.mb-0 Suministros. 
            span De estos gastos hace parte el suministro de servicios como la electricidad, el agua, la telefona o el gas.

          .tarjeta.fondo24.p-3(x="75.7%" y="54.8%" numero="+")
            p.text-bold.mb-0 Gastos variables
            span Relacionados con la actividad propia de cada empresa y el funcionamiento del negocio, a veces estos gastos son ms difciles de prever y contabilizar.
            br
            span Ejemplos: materia prima, tercerizacin de servicios
            br
            br
            span.text-bold.mb-0 Impuestos. 
            span Aqu se contempla todo lo referente a tributos municipales o autonmicos y las tasas que deban pagarse, de acuerdo a la actividad especfica de cada empresa.
            br
            br
            span.text-bold.mb-0 Materia prima. 
            span A medida que aumenta el volumen de ventas, aumenta tambin el coste de produccin, puesto que una mayor cantidad de materia prima es requerida para responder a la demanda.
            br
            br
            span.text-bold.mb-0 Comunicacin externa. 
            span Con el objetivo de darse a conocer, promocionar sus productos y servicios y reforzar su marca, una empresa puede requerir dedicar parte de su presupuesto mensual a la comunicacin externa.
            br
            br
            span.text-bold.mb-0 Subcontratacin de servicios. 
            span Dependiendo de la actividad de tu empresa y el modelo de tu negocio, es posible que debas contratar servicios como el transporte de las materias primas o de los productos terminados.

    .row.justify-content-around.align-items-center.mt-3
      .col-xxl-10.col-xl-10.col-11.col-md-11(data-aos="fade-up-left")
        .tarjeta.fondo8.p-3.mb-5
          .row.justify-content-center.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/tema1/figura2.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p Una vez se han establecido los costos y los gastos, se pueden usar diferentes estrategias para definir el precio, tal como se nos indica en el documento anexo de la Universidad de Alicante. Facultad de ciencias econmicas y empresariales, que puede acceder en el enlace: 
                .col-sm-auto
                  a.boton.color-acento-botones.texto-blanco(href="https://rua.ua.es/dspace/bitstream/10045/16114/8/Tema%208.pdf" target="_blank")
                    span Ingresar
