    separador
    #agencias.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.11 Agencias de viaje
    .bloque-texto-g.fondo25.p-3.p-sm-4.p-md-5(data-aos="fade-up-left")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/imagen15.jpg')})`}"
      )
      .bloque-texto-g__texto.py-5.px-4
        p.mb-0 La historia de las agencias de viaje se remonta a mediados del siglo XIX, cuando Thomas Cook, residente de <i>Leicester</i>, Inglaterra, incit a sus conocidos a participar en un evento acadmico en la ciudad de <i>Loughborough</i>; para tal fin contrat un tren que los llevara en el recorrido de ida y regreso; de esta forma evit el desgaste de todos, en cuanto a la bsqueda de tiquetes de viaje, atender horarios, etc. Una vez estuvo organizado todo, 540 personas contrataron el servicio, a pesar de no haber realizado la operacin de tal servicio con la intencin de generar ganancias, Thomas evidenci el potencial empresarial en dicha actividad y cre la primera agencia de viajes del mundo; por ello se le conoce como uno de los precursores en turismo.
    p.mt-5 En el siguiente recurso se puede observar los diferentes tipos de agencias de viaje y funciones de cada uno de estos: 
    .row.justify-content-center.my-5
        .col-xxl-6.col-lg-6.col-md-11.col-sm-11.col-11.mt-4(data-aos="fade-up-left")
          LineaTiempoD.color-secundario
            p(numero="1" titulo="Agencias de viajes y turismo") Son las empresas comerciales, debidamente constituidas por personas naturales o jurdicas que se dediquen profesionalmente a vender planes tursticos.<br/><span style="margin-left: 5px;">Funciones:</span><br/><ul style="color: margin-left: 10px;"><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Organizar, promover y vender planes tursticos nacionales, para ser operados por las agencias de viajes operadoras establecidas legalmente en el pas.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Organizar, promover y vender planes tursticos para ser operados fuera del territorio nacional.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Reservar y contratar alojamiento y dems servicios tursticos.</span></li><li><span style="color: #12263F;">Reservar cupos y vender pasajes nacionales e internacionales en cualquier medio de transporte.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Operar turismo receptivo, para lo cual debern contar con un departamento de turismo receptivo y cumplir con las funciones propias de las agencias de viajes operadoras.</span></li></ul>

            p(numero="2" titulo="Agencias de viajes operadoras") Son las empresas comerciales, debidamente constituidas por personas naturales o jurdicas que se dediquen profesionalmente a operar planes tursticos.<br/><span style="margin-left: 5px;">Funciones:</span><br/><ul style="color: margin-left: 10px;"><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Operar dentro del pas planes tursticos, programados por agencias de viajes del exterior y del pas.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Organizar y promover planes tursticos para ser operados por ellas mismas, sus sucursales y agencias si las tuviere, de acuerdo con la ubicacin de cada una de ellas dentro del territorio nacional.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Prestar los servicios de transporte turstico de acuerdo con las disposiciones que reglamentan la materia.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Brindar equipo especializado tal como implementos de caza y pesca, buceo y otros elementos deportivos, cuando la actividad lo requiera.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Prestar el servicio de guianza con personas debidamente inscritas en el Registro Nacional de Turismo.</span></li></ul>
            
            p(numero="3" titulo="Agencias de viajes mayoristas") Son las empresas comerciales, debidamente constituidas por personas naturales o jurdicas, que se dediquen profesionalmente a programar y organizar planes tursticos.<br/><span style="margin-left: 5px;">Funciones:</span><br/><ul style="margin-left: 10px;"><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Programar y organizar planes tursticos nacionales e internacionales, para ser ejecutados o por agencias de viajes operadoras y vendidos por las agencias de viajes y turismo.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Programar y organizar planes tursticos para ser operados fuera del territorio nacional por sus corresponsales o agentes y para ser vendidos por las agencias de viajes y turismo.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Promover y vender planes tursticos hacia Colombia, para ser ejecutados por las agencias operadoras establecidas en el pas.</span></li><li><span style="color: #12263F;"><i class="fas fa-circle fa-xs" style="margin-right: 5px;"></i>Reservar y contratar alojamiento y dems servicios tursticos, para ser vendidos por las agencias de viajes y turismo.</span></li></ul>
        .col-xxl-6.col-lg-6.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
          img.mt-4(src="@/assets/curso/tema1/imagen16.jpg")
    .row(data-aos="fade-up-left")
      .col-xxl-4.col-xl-5.col-lg-5.col-md-7.col-sm-11.col-12.fondomundo
        h4.ps-5.pt-5.ms-5 <i>Online Travel Agency</i> - OTA
    p.my-5(data-aos="fade-up-left") Las OTA por su definicin en ingls (<i>Online Travel Agency</i>) o Agencias de Viajes <i>Online</i>, son sitios <i>web</i> enfocados a vender servicios tursticos ofrecidos por sus socios o proveedores como lo son aerolneas, empresas <i>rent car</i> o de alquiler de vehculos, hoteles, hostales, organizadores de eventos y ms; quienes le pagan un porcentaje de comisin a las OTA al momento de recibir una reserva por parte del cliente a travs de su portal.

    .row.justify-content-center.alineacion
      .col-xxl-6.col-xl-6.col-lg-6.col-md-6.col-sm-11.col-11(data-aos="fade-up-left")
        img.mt-4.altura(src="@/assets/curso/tema1/imagen17.jpg")
      .col-xxl-6.col-xl-6.col-lg-6.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
        .cajon.color-primario.p-4.mb-4.mt-4.fondo1
          p De una manera ms simple, se puede decir que un OTA es un sitio <i>web</i> donde los interesados en viajar pueden buscar y comparar los precios de los servicios tursticos que desean adquirir, para posteriormente realizar su reserva sin la necesidad de tener un agente de viajes de manera presencial. As pues, estos portales cuentan con grandes fortalezas ya que pueden tener un alcance mayor y en menor tiempo que una agencia tradicional o fsica; por ello, las pequeas empresas del sector o incluso quienes ofrecen sus servicios de manera independiente, pueden beneficiarse de las OTA por la visibilidad y alcance en lnea que sta les ofrece.
        p.fondo26 Internet les permite reunir la oferta mundial de viajes en un solo lugar, para que los turistas reserven sus propios viajes <i>online</i>. Son una herramienta de autoservicio que han cambiado la manera en que se buscan y reservan los viajes, porque en vez de tener que recurrir a una agencia en fsico, los viajeros tienen mayor facilidad para planear y reservar vuelos, alojamiento y visitas guiadas por su cuenta.
    .row.justify-content-center
      .col-xxl-8.col-xl-8.col-lg-8.col-md-10.col-sm-11.col-11(data-aos="fade-up-left")
        .bloque-texto-b.color-primario.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            h4.mb-0 La primer OTA naci en 1996 en Estados Unidos y es la conocida Expedia; que cre un modelo de negocios revolucionario para la poca y al cabo de tres aos se estableci como una de las diez agencias de viajes con ms ventas en aquel pas.
            i.fas.fa-quote-right
    p.my-5(data-aos="fade-up-left") Este tipo de sitios se basan en comentarios y crticas sobre los servicios que ofrecen puntualmente, por ejemplo: <i>TripAdvisor</i> muestra las opiniones sobre destinos tursticos, hoteles y restaurantes; Despegar.com muestra vuelos y hoteles; y <i>Airbnb</i> ofrece informacin sobre el alquiler de casas o apartamentos a precios realmente econmicos.
    .row.justify-content-center.alineacion
      .col-xxl-6.col-xl-6.col-lg-6.col-md-11.col-sm-11.col-11(data-aos="fade-up-left")
        p La mayora de las OTA funcionan como una red social que se nutre a partir de los comentarios, fotografas y opiniones de los turistas que desean compartir su opinin al respecto y las imgenes de lo vivido durante su recorrido en el destino elegido. Lo diferencial en estos tipos de agencias de viaje, es que los turistas son quienes realizan la bsqueda de los servicios que desean contratar y tienen el poder de comparar y seleccionar el que ms les convenga segn su presupuesto; por ello, en este tipo de OTA el principal canal de reservas y/o compras es el internet y no un empleado. 
        p No es entonces un secreto que el trabajo de las OTA ha cobrado gran importancia a nivel mundial ya que hoy por hoy son la primera parada de los cibernautas a la hora de planear sus viajes. Estas <i>webs</i> brindan valor en cada fase del proceso de compra:  incorporan la necesidad de los usuarios, realizan la bsqueda de informacin, presentan alternativas, influyen en la decisin de compra y tambin buscan conocer el desenlace posterior a sta. Su importancia tiene hoy un gran peso ya que los turistas rescatan el acceso global que se les brinda, sin importar la ubicacin donde se encuentren; la transparencia en los precios; la competitividad y la confianza que obtienen al realizar su compra. Se invita a consultar el siguiente recurso, en donde se podr conocer un poco ms sobre las OTA a nivel mundial:
      .col-xxl-6.col-xl-6.col-lg-6.col-md-7.col-sm-11.col-11(data-aos="fade-up-right")
        img.altura(src="@/assets/curso/tema1/imagen18.jpg")

    .row.justify-content-center.alineacion
      .col-xxl-6.col-xl-6.col-lg-6.col-md-6.col-sm-11.col-11(data-aos="fade-up-left")
        img.altura.mt-4(src="@/assets/curso/tema1/figura4.svg")
      .col-xxl-6.col-xl-6.col-lg-6.col-md-10.col-sm-11.col-11(data-aos="fade-up-right")
        //-slider    
        .tarjeta.tarjeta--gris.px-4.my-4.pt-4
          SlyderA.otroslide2(tipo="b")
            .row.justify-content-center
              .col-lg-4.col-md-4.col-5
                figure
                  img(src='@/assets/curso/tema1/agoda.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 Fundada en el ao 1998 y adquirida por <i>Priceline Group</i> (ahora conocido como <i>Booking Holdings</i>) en 2007. El sitio <i>web</i> de se encuentra disponible en 38 idiomas y su fuerte son los anuncios de todo tipo de propiedades. Es una de las mayores agencias de viaje <i>online</i> en el mercado asitico, pero contina expandindose en los mercados de Estados Unidos, la India y Europa.
                p Es ideal para: todas las categoras de alojamiento.

            .row.justify-content-center
              .col-lg-3.col-md-3.col-4
                figure
                  img(src='@/assets/curso/tema1/airbnb.svg')
              .col-lg-12.col-md-12.mb-md-0.px-4
                p.mt-4 Se conoce por brindar acceso a alojamientos nicos en el mundo entero. La mayora de propiedades que usan <i>Airbnb</i> no hacen parte de los principales segmentos de hotelera, sino que estn ms orientadas a la cultura y vida local.
                p Ideal para: alquileres vacacionales, <i>bed and breakfasts</i>, alojamientos compartidos, hoteles <i>boutique</i>, hostales y propiedades independiente.

            .row.justify-content-center
              .col-lg-4.col-md-4.col-5.col-sm-5
                figure
                  img(src='@/assets/curso/tema1/booking.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 Es uno de los sitios <i>web</i> de viajes ms grandes y conocidos en el mundo entero. Su sitio <i>web</i> y aplicaciones mviles son accesibles en 43 idiomas y renen ms de 50 millones de comentarios de huspedes verificados en ms de 200 pases. Sus tasas de comisin son generalmente ms bajas que las de otras OTA.
                p Ideal para: todo tipo de alojamientos, incluyendo <i>resorts</i>, villas, cabaas, hoteles, apartamentos, hostales, alquileres vacacionales, B&B y ms.

            .row.justify-content-center
              .col-lg-3.col-md-3.col-4
                figure
                  img(src='@/assets/curso/tema1/expedia.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-4 Incluye 200 sitios <i>web</i> de reservas de viajes en 75 pases y en 35 idiomas. Su sitio <i>web</i> recibe ms de 600 millones de visitas por mes. Incluye bsquedas para todo tipo de alojamientos y vuelos, alquiler de automviles, cruceros y paquetes promocionales. Al habilitar la conexin a <i>Expedia</i>, las propiedades se anuncian en <i>Hotels.com</i>, <i>Wotif</i> y muchos otros canales.
                p Ideal para: alojamientos de toda clase y tamao en el mundo entero.

            .row.justify-content-center
              .col-lg-5.col-md-6.col-sm-7.col-7
                figure
                  img(src='@/assets/curso/tema1/hotel.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 Es la mayor OTA para alojamientos de hostales en el mundo entero. Cuenta con ms de 12 millones de comentarios de usuarios. Su inventario incluye hoteles, B&B y dems alojamientos de distintos presupuestos. Su aplicacin y sitio <i>web</i> estn en 20 idiomas distintos.
                p Ideal para: hostales, hoteles, <i>bed and breakfast</i>, casas de huspedes, apartamentos y zonas de <i>camping</i>.

            .row.justify-content-center
              .col-lg-5.col-md-6.col-sm-7.col-7
                figure
                  img(src='@/assets/curso/tema1/despegar.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 Es la OTA ms grande de Amrica Latina, sirviendo a toda la regin y ms all. Se especializa en hoteles y vuelos. Sin embargo, tambin ofrece alquileres de vacaciones, alquiler de automviles, actividades y paquetes.
                p Ideal para: hoteles y aerolneas.

            .row.justify-content-center
              .col-lg-4.col-md-5.col-6
                figure
                  img(src='@/assets/curso/tema1/travel.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 Se especializa en reservas de hoteles y vuelos, ofreciendo tambin paquetes y <i>tours</i>. Aparte, esta OTA tambin ofrece reservas en algunos de los principales destinos de Mxico, Estados Unidos, Repblica Dominicana y el Caribe.
                p Ideal para: hoteles y aerolneas.

            .row.justify-content-center
              .col-lg-3.col-md-3.col-4
                figure
                  img(src='@/assets/curso/tema1/tiquetes.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 Es propiedad de PriceTravel, y cuenta con sus mismas conexiones. Principalmente, es una OTA de vuelos que tambin ofrece reservas de hotel, traslados al aeropuerto, alquiler de automviles, ofertas de paquetes, boletos de autobs, cruceros y excursiones a precios competitivos.
                p Ideal para: hoteles y aerolneas.
    .row
      .col-xxl-4.col-xl-5.col-lg-5.col-md-7.col-sm-9.col-9.fondomundo(data-aos="fade-up-left")
        h4.ps-5.pt-5.ms-5 Metabuscadores

    .row.justify-content-center.my-5
      .col-xxl-5.col-xl-5.col-lg-7.col-md-11.col-sm-11.col-11(data-aos="fade-up-left")
        .cajon.puntacajon.p-4.mb-4.fondo17.sombracajon
          p Los metabuscadores son herramientas de bsqueda avanzada que sirven para consultar informacin y poder obtener de manera rpida los datos recopilados de diferentes fuentes. En turismo, estos metabuscadores hacen uso de la informacin de otros motores de bsqueda globales como <i>Google</i>, pginas <i>web</i> de hoteles e incluso los mismos sitios <i>web</i> de las OTA, para agrupar y presentarle al cibernauta los precios de muchos hoteles de un solo destino, de manera actualizada y prctica para que el turista compare las tarifas y elija entre las diferentes opciones.
        p Los resultados que se pueden hallar en los metabuscadores se filtran por productos o servicios buscados por el usuario; por ello, este proceso se convierte en algo ms sencillo y rpido donde en un solo portal <i>web</i>, se encuentran las opciones de varios establecimientos. As pues, este tipo de herramientas permiten acceder a unos datos ms completos con una sola bsqueda, impidiendo reprocesos y desgaste en el tiempo del viajero.  
      .col-xxl-7.col-xl-7.col-lg-5.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
        p En turismo los dos metabuscadores ms usuales son <i>Tripadvisor</i> o <i>Kayak</i>. En la figura 7 se puede ver un ejemplo de bsqueda:
        .titulo-sexto.color-acento-contenido
          span.text-bold.mb-0 Figura 7 
          span Ejemplo de cmo funcionan el metabuscador <i>Tripadvisor</i>    
        img(src='@/assets/curso/tema1/imagen19.jpg')
      
    .row.mb-5
      .col-xxl-12.fondonaranja.py-4.px-5(data-aos="fade-up-left")
        p.mb-0 Como se observa, al ingresar fecha de llegada y de salida y el tipo de habitacin, el metabuscador <i>Tripadvisor</i> encuentra diferentes opciones de OTA como <i>Booking</i>, <i>Expedia</i> y Hoteles.com con sus respectivos precios, para que el usuario elija el que ms se acomoda a su bolsillo y a su gusto. Ac es importante destacar que el metabuscador busca la informacin, pero no vende; sin embargo, agiliza el trabajo del usuario ya que ste puede ir directamente al sitio de la OTA a comprar y no tener que detenerse haciendo dos, tres o ms bsquedas para comparar precios.   

    p.mb-5(data-aos="fade-up-left") Existen diferencias entre un metabuscador y una OTA, tal como se presenta a continuacin:
    .row.justify-content-center
      .col-xxl-6.col-xl-7.col-lg-8.col-md-10.col-12.fondo2.mb-5(data-aos="fade-up-left")
        h3.text-white.text-center.pt-2.pb-3.mb-0 Diferencias entre un metabuscador y una OTA's 
    TabsA.color-acento-botones.mb-5(data-aos="fade-up-left")
      .tarjeta.p-4.fondo27(titulo="Reserva")
        h4 Reserva
        p Una OTA efecta la reserva desde su propia web; un metabuscador no tiene medios tcnicos para confirmar o gestionar reservas de ningn tipo. Lo que hacen, en cambio, es llevar trfico a los sitios que s que pueden <i>web</i> de la OTA o de la empresa proveedora. 
      
      .tarjeta.fondo27.p-4(titulo="<i>Marketing</i>")
        h4 <i>Marketing</i>
        p Las OTA's  son muy necesarias para la distribucin de cualquier hotel bien sea grande o pequeo, ya que les ayudan a ampliar y alcanzar nuevos mercados gracias a la inversin cuantiosa que realizan en <i>marketing</i>. <br/>Una OTA vende las habitaciones de los hoteles mostrando sus caractersticas, su valor econmico y las imgenes llamativas para completar la reserva desde dicho portal <i>web</i>.  
      
      .tarjeta.p-4.fondo27(titulo="Factor econmico")
        h4 Factor econmico
        p Un metabuscador agrupa los valores econmicos de los servicios ofrecidos por las OTA's y se los muestra a los turistas como una lista; de esta forma el proceso de eleccin se vuelve ms gil y fcil ya que se pueden contrastar los precios a fin de elegir la habitacin a reservar, por ejemplo. La diferencia ms importante es que los metabuscadores no venden su propio inventario.

      .tarjeta.p-4.fondo27(titulo="Pago de comisin")
        h4 Pago de comisin
        p Cuando la OTA realiza una venta desde su propia pgina <i>web</i>, el proveedor a quien le entr esa venta (por ejemplo, un hotel), debe pagar un porcentaje de comisin por utilizar el canal de la agencia de viajes virtual. Ac se puede ver otra diferencia con el metabuscador: los pequeos hoteles pueden aumentar sus ventas directas ya que el usuario solo encontrar su valor, pero no podr comprar; por tanto, se tendr que dirigir directamente a la pgina de la empresa a la que le desea contratar el servicio.
    .row.justify-content-center.mb-5
      .col-xxl-10.col-xl-10.col-lg-10.col-md-11.col-sm-11.col-11(data-aos="fade-up-left")
        .cajon.puntacajon.p-4.mb-4.fondo17.sombracajon
          h4.mb-3.mx-3 Importante
          p.mb-0.mx-3 Una diferencia significativa entre metabuscadores y las OTA es que mientras los metabuscadores muestran informacin, ellos venden los alojamientos.
    p.mb-5(data-aos="fade-up-left") A nivel de las ventajas que se tiene al utilizar los metabuscadores se puede encontrar que:
    .row.justify-content-center
      .col-xxl-6.col-xl-6.col-lg-7.col-md-8.col-sm-11.col-11(data-aos="fade-up-left")
        ul.lista-ul
          li.otrocolorcheck
            i.fas.fa-check
            p El metabuscador es mucho ms rpido ya que le permite al cliente buscar a la vez distintas <i>web</i>, posibilitando comparar precios en un solo vistazo, ahorrando tiempo y complejidad.
          li.otrocolorcheck
            i.fas.fa-check
            p Un metabuscador permite a los hoteles pequeos competir en igualdad de condiciones con hoteles y cadenas ms grandes.
          li.otrocolorcheck 
            i.fas.fa-check
            p Aumenta las reservas directas ya que al mostrarles la comparacin entre los precios de las OTA y el real del hotel; existen ms probabilidades de que el turista decida hacer la reserva directamente con el establecimiento de alojamiento.
          li.otrocolorcheck 
            i.fas.fa-check
            p Los metabuscadores funcionan con un sistema PPC (Pago Por Clic) que suele ser ms barato que un sistema basado en comisiones como el de las OTA.
          li.otrocolorcheck 
            i.fas.fa-check
            p Un metabuscador ofrece un sentimiento de confianza y seguridad al turista ya que le muestra los comentarios a otras personas que han contratado el mismo servicio turstico anteriormente.   
      .col-xxl-6.col-xl-6.col-lg-5.col-md-4.col-sm-11.col-11(data-aos="fade-up-right")
        img(src='@/assets/curso/tema1/imagen20.jpg')
    p.my-5(data-aos="fade-up-left") Entre los principales metabuscadores en turismo se encuentran Tripadvisor, Trivago, Kayak. Google Hotel Ads, en la siguiente figura 8 se encuentra una explicacin ms detallada al respecto:
