      .col-xxl-3.col-xl-3.col-lg-3.col-md-6.col-sm-6.col-11.mt-4(data-aos="fade-up-left")
        img(src='@/assets/curso/tema1/triadvisor.svg')
      .col-xxl-3.col-xl-3.col-lg-3.col-md-6.col-sm-6.col-11.mt-4(data-aos="fade-up")
        img(src='@/assets/curso/tema1/trivago.svg')
      .col-xxl-3.col-xl-3.col-lg-3.col-md-6.col-sm-6.col-11.mt-4(data-aos="fade-up")
        img(src='@/assets/curso/tema1/kayak.svg')
      .col-xxl-3.col-xl-3.col-lg-3.col-md-6.col-sm-6.col-11.mt-4(data-aos="fade-up-right")
        img(src='@/assets/curso/tema1/google.svg')
    p.text-center.my-5(data-aos="fade-up-left") Nota. SENA (2022).

    .row
      .col-xxl-7.col-xl-8.col-lg-9.col-md-12.col-sm-12.col-12.fondomundo2.pt-4.py-xs-1(data-aos="fade-up-left")
        h4.ps-4.ms-5.mt-3.mb-2.mt-lg-1 <i>Customer Relationship Management y Enterprise Resource Planning</i>
    p.my-5(data-aos="fade-up-left") Estas dos herramientas permiten la gestin del servicio y la relacin que ejerce con el cliente o los clientes interesados, as como la fidelizacin de estos, posibilitando perdurabilidad en el tiempo, a continuacin, se explica cada una de estas:
    p.subrayar.mb-5(data-aos="fade-up-left") <i>Customer Relationship Management.</i>
    p.mb-5(data-aos="fade-up-left") Este trmino se refiere a una herramienta diseada para gestionar las estrategias y prcticas de negocio que estn dirigidas a la relacin con el cliente y prospectos. Son <i>software</i> especializados de gran ayuda para los equipos de ventas y <i>marketing</i>. Observar el siguiente video para aprender ms al respecto. 
    figure.mb-5(data-aos="fade-up")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/PF_yA02FGIM" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    p.mb-5(data-aos="fade-up-left") Anteriormente las compaas usaban mtodos tradicionales para poder gestionar sus relaciones con clientes; era tradicional encontrar <i>post it</i>, cuadernos, agendas fsicas, hojas de clculo, etc., con la informacin de sus usuarios y con las tareas a desarrollar para agendar reuniones con ellos, llamarlos e incluso cobrarles. Hoy da, esto se hace de manera mucho ms eficiente y tecnolgica con la ayuda de un CRM. Hay diferentes recursos en el mercado, sin embargo, la decisin de compra depender de los requerimientos de cada empresa. Estas diferencias pueden establecerse como tipos de CRM, tal como se muestra en la siguiente figura 9:
    .titulo-sexto.color-acento-contenido(data-aos="fade-up-left")
      span.text-bold.mb-0 Figura 9 
      span tipos de CRM
    .row.justify-content-center
      .col-xxl-3.col-xl-3.col-lg-4.col-md-6.col-sm-11.col-11(data-aos="fade-up-left")
        img(src='@/assets/curso/tema1/crm1.svg')
      .col-xxl-3.col-xl-3.col-lg-4.col-md-6.col-sm-11.col-11(data-aos="fade-up")
        img(src='@/assets/curso/tema1/crm2.svg')
      .col-xxl-3.col-xl-3.col-lg-4.col-md-6.col-sm-11.col-11(data-aos="fade-up-right")
        img(src='@/assets/curso/tema1/crm3.svg')

    p.text-center.my-5(data-aos="fade-up-left") Nota. SENA (2022).

    p.subrayar.mb-5 <i>Enterprise Resoure Planning - ERP.</i>
    .row.justify-content-center
      .col-xxl-6.col-xl-6.col-lg-7.col-md-11.col-sm-11.col-11(data-aos="fade-up-left")
        p La globalizacin y los cambios constantes del entorno y los mercados han conllevado a que las empresas tomen consciencia sobre la digitalizacin de sus procesos estratgicos. Este panorama no ha sido ajeno al sector turstico, por el contrario, hoy por hoy los prestadores de servicios tursticos han ido implementado herramientas digitales con el objetivo de ser ms eficientes y competitivos, prestando un mejor servicio a sus clientes.
        p El trmino ERP se refiere a <i>Enterprise Resource Planning</i>, que significa sistema de planificacin de recursos empresariales. Es un <i>software</i> integrado por mdulos que administra diferentes procesos internos de la compaa como las compras, la contabilidad, la gerencia de proyectos, la gestin de riesgos y los procedimientos de la cadena de suministros. 
        p El objetivo primordial de un ERP es agrupar la informacin que proviene de las distintas reas de la empresa, evitando que sta se duplique y generando as, mayor fluidez de la informacin y menos reprocesos para quienes la gestionan.
      .col-xxl-6.col-xl-6.col-lg-5.col-md-9.col-sm-11.col-11(data-aos="fade-up-right")
        img(src='@/assets/curso/tema1/imagen21.jpg')
    p.mt-5(data-aos="fade-up-right") Entre las ventajas de un ERP se pueden apreciar las siguientes (ver figura 10):
    .row.fondo28.ampliarambos.px-5
      .titulo-sexto.color-acento-contenido.my-5(data-aos="fade-up-right")
        span.text-bold.mb-0 Figura 10. 
        span Ventajas de un ERP
      .row.justify-content-center
        .col-xxl-2.col-xl-2.col-lg-3.col-md-4.col-sm-5.col-6.mb-2(data-aos="fade-up-right")
          img(src='@/assets/curso/tema1/erp1.svg')
        .col-xxl-6.col-xl-7.col-lg-8.col-md-12.mb-2.my-2(data-aos="fade-up-left")
          img(src='@/assets/curso/tema1/erp2.svg')
          .row.justify-content-center.my-2
            .col-xxl-6.col-xl-6.col-lg-6.col-md-10.col-sm-10.mb-2
              img(src='@/assets/curso/tema1/erp3-1.svg')
            .col-xxl-6.col-xl-6.col-lg-6.col-md-10.col-sm-10.mb-2
              img(src='@/assets/curso/tema1/erp3-2.svg')
          .row.justify-content-center.my-2
            .col-xxl-6.col-xl-6.col-lg-6.col-md-10.col-sm-10.mb-2
              img(src='@/assets/curso/tema1/erp4-1.svg')
            .col-xxl-6.col-xl-6.col-lg-6.col-md-10.col-sm-10.mb-2
              img(src='@/assets/curso/tema1/erp4-2.svg')

      p.text-center.my-5(data-aos="fade-up-right") Nota. SENA (2022).
    p.mb-5(data-aos="fade-up-right") Debido a sus mltiples funciones y beneficios, hoy da existen muchas compaas que ofrecen diferentes tipos de ERP; sin embargo, cuando se analizan sus ofertas, se pueden encontrar muchas similitudes en los servicios, por ello, es importante diferenciar los tipos de ERP que existen y que las compaas tengan presentes sus necesidades a la hora de adquirir uno, como se muestra a continuacin:
    .row.justify-content-center
      .col-xxl-6.col-xl-6.col-lg-7.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
        AcordionA.otroacordion.otratarjetaazul(tipo="b" clase-tarjeta="tarjeta tarjeta--azul")
          .row(titulo="Segn el diseo")
            .col-md-11
              p Se divide en:
              p A medida: se crea desde 0 para una empresa en particular. Es una solucin ms costosa y que exige encontrar a la compaa adecuada para llevarla a cabo, pero a cambio ofrece la mxima adaptabilidad.
              p Predefinido: son ms baratos y rpidos de implementar. Se contratan por mdulos o paquetes en funcin de las necesidades de cada empresa.

          .row(titulo="Segn la instalacin")
            .col-md-11
              p Se divide en dos:
              p Local: se instalan en el entramado informtico de la propia empresa para que pueda ser utilizado en uno o varios equipos dentro de la sede del negocio.
              p En la Nube: se puede usar desde cualquier lugar donde haya una conexin a internet, porque est ubicado en un servidor concreto. Esto significa que su funcionamiento no se reduce solamente a la sede de la empresa, sino que permite una mayor versatilidad en este sentido.

      .col-xxl-6.col-xl-6.col-lg-5.col-md-8.col-sm-11.col-11(data-aos="fade-up-left")
        img(src='@/assets/curso/tema1/imagen22.jpg')

    p.mt-5(data-aos="fade-up-right") Entre los valores ms buscados de los ERP se encuentran, su flexibilidad frente a los cambios que puede presentar la compaa y sus procesos, la facilidad de uso, el mantenimiento, el precio de licencias y de mantenimiento, los costos de instalacin y lo ms importante, en realidad que el fabricante conozca del sector turismo y que el <i>software</i> se adece al mismo.

    separador
    #paquetes.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.12 Paquetes tursticos
    p(data-aos="fade-up-right") Es un conjunto de servicios como alojamiento, transporte, alimentacin, etc. que se le ofrecen a un cliente y que son contratados con base a un itinerario organizado previamente, a un precio nico y global. Estos servicios pueden pertenecer a la agencia de viajes que ofrece el paquete turstico o pueden ser contratados con los diferentes prestadores de servicios tursticos para posibilitar al usuario su consumo y disfrute.
    p.mb-5(data-aos="fade-up-right") Cuando se habla de itinerario se hace referencia al recorrido, y al tipo de actividades y servicios incluidos en el paquete (ver tabla 5):
    .titulo-sexto.color-acento-contenido(data-aos="fade-up-right")
      span.text-small.mb-0.text-bold Tabla 5 
      span.text-small.mb-0 Itinerario de un paquete turstico
    .tabla-c(data-aos="fade-up")
      table
        tr.fondo14.align-items-top
          th.bordetabla 
            img.dimensiones(src='@/assets/curso/tema1/calendario.svg')
          td.bordetabla
            img.dimensiones(src='@/assets/curso/tema1/lugares.svg')
          td.bordetabla
            img.dimensiones(src='@/assets/curso/tema1/bus.svg')
          td.bordetabla
