          td.bordetabla
            img.dimensiones(src='@/assets/curso/tema1/reloj.svg')
          td.bordetabla
            img.dimensiones(src='@/assets/curso/tema1/huellas.svg')
          td
            img.dimensiones(src='@/assets/curso/tema1/pesos.svg')
        tr
          th.bordetabla.alinearArriba
            p.text-center.size15px Fechas y horarios para llevar cabo cada una de las actividades que incluye el paquete turstico.
          td.alinearArriba.bordetabla
            p.text-center.size15px Lugares de salida y regreso.
          td.alinearArriba.bordetabla
            p.text-center.size15px Caractersticas de los transportes que van a ser utilizados para el traslado de los viajeros.
          td.alinearArriba.bordetabla
            p.text-center.size15px Alojamiento, con el detalle de las caractersticas, categora y comodidades que ofrece.
          td.alinearArriba.bordetabla
            p.text-center.size15px Horario de comidas en el caso de que incluya.
          td.alinearArriba.bordetabla
            p.text-center.size15px Nombre y detalle de cada una de las visitas o excursiones.
          td.alinearArriba
            p.text-center.size15px Precio y detalles de la empresa que lo organiza.
    p.mb-5.mt-3.text-center(data-aos="fade-up") Nota. SENA (2022).
    p(data-aos="fade-up") El recorrido presenta las rutas o caminos a tomar, los lugares que se visitarn y tambin donde se pasar la noche, el tipo de transporte que se usar para ir de un lugar a otro, y las distancias y tiempos que se usarn para tales desplazamientos.
    p.mb-5(data-aos="fade-up") Para el diseo de todo paquete es necesario determinar tambin el orden en que se ofrecern los servicios, sus tiempos y la asignacin del dinero para que el paquete opere bajo lo establecido y lo requerido por el usuario. Recordar que se debe ofrecer una experiencia completa para el tipo de turistas que se elija y por ello, cada servicio que se incluya en el paquete deber estar asociado a sus necesidades, expectativas, y deseos, tal como se indica a continuacin:
    //-slider
    SlyderF.my-5(data-aos="fade-up-right")(columnas="col-lg-6 col-xl-6")
      .tarjeta.otra.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-4.col-md-4.col-4
            img(src='@/assets/curso/tema1/slider2-1.png')
        h3.text-dark Programas de estancia
        p.text-dark Incluyen la combinacin de un medio de transporte y una estancia en algn alojamiento turstico. No suele haber horarios o excursiones preestablecidas. Se usa para playas, campo o montaas.

      .tarjeta.otra.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-4.col-md-4.col-4
            img(src='@/assets/curso/tema1/slider2-2.png')
        h3.text-dark Circuitos
        p.text-dark Se visitan un conjunto de destinos de manera articulada y programada. Suelen clasificarse de acuerdo al medio de transporte que utilicen.
      .tarjeta.otra.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-4.col-md-4.col-4
            img(src='@/assets/curso/tema1/slider2-3.png')
        h3.text-dark Circuitos terrestres
        p.text-dark Generalmente son <i>tours</i> en autobs para viajes dentro del territorio nacional o pases limtrofes.
      .tarjeta.otra.p-4.fondo19
        .row.justify-content-left.mb-3
          .col-xxl-4.col-md-4.col-4
            img(src='@/assets/curso/tema1/slider2-4.png')
        h3.text-dark Circuitos aeroterrestres
        p.text-dark El paquete incluye el vuelo areo para llegar al destino turstico determinado donde se desarrolla el resto de los servicios tursticos: paseos, alojamiento, visitas guiadas, etc.; y luego se utiliza nuevamente el avin para volver.
      .tarjeta.otra.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-4.col-md-4.col-4
            img(src='@/assets/curso/tema1/slider2-5.png')
        h3.text-dark Larga distancia
        p.text-dark Este tipo de paquetes tursticos suele utilizar transporte y gua particular para llegar al destino.
      .tarjeta.otra.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-4.col-md-4.col-4
            img(src='@/assets/curso/tema1/slider2-6.png')
        h3.text-dark Viajes alternativos
        p.text-dark Estn diseados exclusivamente con destinos inditos, suelen implementar medios de transporte o alojamiento inslitos o poco frecuentes. El objetivo de estos paquetes es ofrecer actividades y experiencias diferentes.
      .tarjeta.otra.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-4.col-md-4.col-4
            img(src='@/assets/curso/tema1/slider2-7.png')
        h3.text-dark Viajes a medida
        p.text-dark De acuerdo a los requerimientos o preferencias del cliente, incluyendo en el producto final todos los servicios necesarios como transporte, alojamiento y experiencias.
    .row.fondo29.ampliarambos
      .col-xxl-6.col-xl-6.col-lg-7.col-md-11.col-sm-11.col-11.px-5(data-aos="fade-up-right")
        p.mb-4 Los elementos fundamentales de un paquete turstico deben tener en cuenta:
        ul.lista-ul--color
          li.otrocolorcheck 
            i.fas.fa-check
            | Atractivo: opciones de lugares naturales, culturales, histricos o cientficos que el turista pueda elegir segn sus gustos y conveniencia.
          li.otrocolorcheck
            i.fas.fa-check
            | Facilidades: horarios, tipo de financiacin, lugares, precios, hospedaje, esparcimiento, alimentacin, y otros.
          li.otrocolorcheck 
            i.fas.fa-check
            | Transporte: accesible al cliente y sus gustos y/o necesidades.    
      .col-xxl-6.col-xl-6.col-lg-5.col-md-11.col-sm-11.col-11.px-5(data-aos="fade-up-left")
        img(src='@/assets/curso/tema1/figura5.svg')
    p.my-5(data-aos="fade-up-right") Adicional a lo anterior, se debe tener presente que cada paquete est conformado por unos servicios bsicos y otros complementarios:

    .row.justify-content-around.fondo31.align-items-center
      .col-xxl-6.col-xl-6.col-lg-6.col-md-11.col-sm-11.col-11.p-5(data-aos="fade-up-right")
        .row.justify-content-start
          .col-xxl-8.col-xl-7.col-lg-7.col-md-11.col-sm-11.col-11
            h4.margen Bsicos
            p Los bsicos son el componente central que subsana las principales necesidades del turista; en este caso, el transporte resuelve la necesidad de trasladar a una persona de un lugar a otro; el alojamiento resuelve la necesidad de descanso y horas de sueo y la alimentacin resuelve la necesidad de satisfacer el hambre nutriendo al cuerpo.
      .col-xxl-6.col-xl-6.col-lg-6.col-md-11.col-sm-11.col-11.pt-5.pb-4.px-5(data-aos="fade-up-left")
        .row.justify-content-end
          .col-xxl-9.col-xl-9.col-lg-9.col-md-12.col-sm-12.col-12
            h4.text-white.margen Complementarios
            p.text-white Los complementarios amplan la percepcin de valor del turista, frente al servicio bsico. Incluir estos servicios depender de las necesidades del viajero, pero generalmente influyen en la diferenciacin y posicionamiento de la agencia de viajes, con respecto a los servicios de la competencia. En este caso podramos hablar de la inclusin de atractivos tursticos, fiestas, recorridos guiados, actividades de recreacin, conocimiento de personajes representativos del destino, entre otros.

    p.my-5(data-aos="fade-up-right") Estos servicios se organizan dependiendo de varios factores, entre los que se deben tener en cuenta los siguientes, como se aprecia en la figura 11 a continuacin:
    .titulo-sexto.color-acento-contenido.my-5(data-aos="fade-up-right")
      span.text-bold.mb-0 Figura 11. 
      span Organizacin de paquetes tursticos
    .row.fondo16.ampliarambos
      .col-xxl-12.p-5
        .row.justify-content-center(data-aos="fade-up-right")
          .col-xxl-4.col-xl-4.col-lg-4.col-md-10.col-sm-10.col-11
            img.mb-5(src='@/assets/curso/tema1/paquete1.svg')
          .col-xxl-4.col-xl-4.col-lg-4.col-md-10.col-sm-10.col-11
            img.mb-5(src='@/assets/curso/tema1/paquete2.svg')
          .col-xxl-3.col-xl-3.col-lg-3.col-md-8.col-sm-8.col-8
            img.mb-5(src='@/assets/curso/tema1/paquete3.svg')
        p.mt-3.mb-5.text-center(data-aos="fade-up-right") Nota. SENA (2022).
        p.mb-5(data-aos="fade-up-right") Teniendo en claro esos puntos se puede seguir la siguiente ruta para crear un paquete turstico, tal como se explica en el siguiente recurso educativo:
    .row.justify-content-center
      .col-xxl-6.col-xl-7.col-lg-8.col-md-10.col-12.fondo2.mb-5(data-aos="fade-up-right")
        h3.text-white.text-center.pt-2.pb-3.mb-0 Ruta para la creacin de un paquete turstico
    //-slider    
    .tarjeta.tarjeta--azul.p-4.mb-5.fondo7(data-aos="fade-up-right")
      SlyderA.pt-4.otroslide3(tipo="b")
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Creacin o diseo del producto - Anlisis de Informacin
            p Se debe trabajar sobre las necesidades del cliente para realizar la investigacin de mercado del producto turstico que ofreceremos. Debemos conocer el perfil del pasajero, sus intereses, sus expectativas, as como tener en claro el tipo de turismo en el cual se especializa la agencia y conocer todos los tipos de actividades tursticas existentes.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider21.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Planificacin del itinerario
            p En este punto se distribuye el tiempo para cada una de las actividades del paquete turstico. Debemos tener en cuenta las distancias a recorrer, tipo de transporte, duracin de las salidas, tiempos de transporte entre ciudades, etc.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider22.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Eleccin de proveedores
            p Es necesario conocer la calidad de la oferta que le brindaremos a los turistas, por ello es necesario tener buena relacin con los proveedores y escoger siempre los mejores a fin de obtener una buena recomendacin y fidelidad de marca por parte del viajero.

          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider23.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Clculo del presupuesto del viaje
            p Algunas cuestiones a tener en cuenta a la hora de cotizar el viaje sern:
            p Que sea competitivo.
            p Precios netos por da y persona (hay que incluir la comisin del proveedor.
            p Incluir los impuestos correspondientes a cada tipo de servicio.
            p Tener en cuenta los cambios en las cotizaciones de divisas.
            p Tener en cuenta la temporada.
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider24.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Clculo del punto de equilibrio
            p Es muy importante establecer el nmero mnimo de plazas a vender para cubrir los costes del viaje. Debemos tener claro cules son los Costos Fijos (el alquiler de un micro para 50 pasajeros o los servicios de un gua, por ejemplo), los Costos Variables (alojamiento y comidas para los pasajeros, por ejemplo) y el precio de venta. Con esos 3 datos, aplicamos la siguiente frmula para determinar ese punto de equilibrio.
            .row.justify-content-center
              .col-xxl-4.col-xl-4.col-lg-5.col-md-6.col-sm-3.col-3
                img(src='@/assets/curso/tema1/formula.svg')
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider25.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Elaboracin y promocin del folleto
            p Debemos hacer uso de un buen diseo grfico para presentarle el paquete turstico a los turistas. Ac debemos tener presente la manera de ofrecer el paquete, bien sea de forma presencial o digital. 
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider26.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Promocin del producto
            p Debemos elegir el medio por el cual se va a promocionar el paquete turstico, bien sea a travs de redes sociales, <i>e-mail</i>, llamadas telefnicas, presencial, etc. Aqu es fundamental contar con una buena base de datos de potenciales clientes (los que siempre nos compran, los que nos compraron alguna vez o los que slo preguntaron), y para ello contar con un CRM turstico es muy importante, porque sabremos exactamente que quiere cada cliente, y nos permitir personalizar las ofertas.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider27.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Formalizacin y realizacin
            p Se debe formalizar el viaje previa reserva del mismo. Aqu comienza la fase de venta del viaje, que es sumamente importante para el cliente, ya que es la etapa donde l confa plenamente en el agente, y hace sus primeros pagos por el servicio que le ofrecern.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider28.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Control de calidad y seguimiento de la venta
            p Se recomienda fuertemente que, luego de finalizado el viaje, se llame al cliente para consultarle como estuvo el mismo, en que podemos mejorar, y cualquier otro dato del viaje y del cliente que sea importante para fidelizar a este ltimo.
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider29.png')
    p.my-5(data-aos="fade-up-right") Existen tres fases ligadas al ciclo de compra del cliente que estn relacionadas con el proceso de venta de un paquete turstico, estas son:
    .row.justify-content-center
      .col-xxl-6.col-xl-6.col-lg-7.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
        AcordionA.otroacordion2.otratarjetaazul2(tipo="b" clase-tarjeta="tarjeta tarjeta--azul")
          .row(titulo="Fase anterior a la compra del paquete turstico")
            .col-md-11
              p Es la fase en la cual el usuario tiene todas las expectativas e imaginarios sobre el viaje que desea y a partir de ello, investiga con otros viajeros que ya han tenido la experiencia para conocer de primera mano, lo bueno y lo no tan bueno del destino y de la empresa con quienes viajaron, dado el caso.

          .row(titulo="Fase durante el consumo del paquete turstico")
            .col-md-11
              p El cliente vive la experiencia ofrecida en el paquete turstico y obtiene una percepcin favorable o desfavorable al respecto.
          .row(titulo="Fase despus del consumo del paquete turstico")
            .col-md-11
              p La experiencia generada se califica y se difunde a otros turistas por medio del voz a voz. En esta fase consolidamos o no, la fidelidad al producto y marca.
      .col-xxl-6.col-xl-6.col-lg-5.col-md-8.col-sm-11.col-11(data-aos="fade-up-left")
        img(src='@/assets/curso/tema1/imagen24.jpg')

    p.mt-5(data-aos="fade-up-right") Vender de manera <i>online</i> se convirti en una necesidad para las empresas que desean hacer crecer sus negocios. En turismo, el escenario virtual es un buen lugar para ofrecer los paquetes tursticos; sin embargo, se debe tener una buena estrategia para lograr la venta y poder llegar al nicho de mercado indicado. En este sentido, el mercadeo digital, cumple una funcin muy importante ya que brinda herramientas para incrementar las ventas de una manera personalizada logrando que el usuario conecte con la experiencia que ofrece la agencia de viajes. 
    p.mb-5(data-aos="fade-up-right") A continuacin, se va pueden ver diferentes consejos para poder dar mayor fuerza a la promocin <i>online</i> de los paquetes tursticos (ver figura 12):
    .titulo-sexto.color-acento-contenido.my-5(data-aos="fade-up-right")
      span.text-bold.mb-0 Figura 12. 
      span Tips para la promocin <i>online</i> de los paquetes tursticos
    .row.fondo30.ampliarambos
      .col-xxl-12.px-5
        .row.justify-content-center.my-5(data-aos="fade-up-right")
          .col-xxl-4.col-xl-4.col-lg-4.fondo32.pt-4.px-4.mx-5.mb-4
            ul.lista-ul--color
              li 
                i.fas.fa-circle
                p El entorno <i>web</i> de la agencia de viajes requiere una optimizacin SEO del contenido, capaz de situar la pgina en las primeras posiciones. Cuanto ms especfico sea el contenido ms efectivo y fcil de posicionar.
          .col-xxl-4.col-xl-4.col-lg-4.fondo32.pt-4.px-4.mb-4
            ul.lista-ul--color
              li 
                i.fas.fa-circle
                p Con el fin de mejorar la relevancia del contenido del sitio <i>web</i> para los usuarios, es bueno incluir palabras clave en el texto relacionadas con las principales cualidades del paquete turstico.
        .row.justify-content-center.mb-5(data-aos="fade-up-right")
          .col-xxl-3.col-xl-3.col-lg-3.fondo33.pt-4.px-4.mx-5.mb-4
            ul.lista-ul--color
              li 
                i.fas.fa-circle
                p Tener perfil en diferentes redes sociales y hacer uso de videos para promover la agencia a bajo costo, atraer visitantes y generar inters del pblico.
          .col-xxl-3.col-xl-3.col-lg-3.fondo34.pt-4.px-4.mx-5.mb-4
            ul.lista-ul--color
              li 
                i.fas.fa-circle
                p Contar con opiniones de turistas en los perfiles de redes sociales, puede ayudar para que nuevos viajeros deseen comprarnos un nuevo producto.
          .col-xxl-3.col-xl-3.col-lg-3.fondo33.pt-4.px-4.mx-5.mb-4
            ul.lista-ul--color
              li 
                i.fas.fa-circle
                p El tratamiento de las valoraciones negativas ha de ser rpido. Para evitar una controversia mayor, la estrategia predeterminada es responder de un modo profesional y emptico.
    p.text-center.mb-5(data-aos="fade-up-right") Nota. SENA (2022).
