    #rutas.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.13 Rutas y circuitos tursticos
    p.mb-5(data-aos="fade-up-right") Hay una diferenciacin entre rutas y circuitos tursticos, dependiendo de las actividades involucradas e itinerario que se aborda, a continuacin, se encontrar una explicacin detallada a este respecto:
    .bloque-texto-g.fondo25.p-3.p-sm-4.p-md-5(data-aos="fade-up-right")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/imagen25.jpg')})`}"
      )
      .bloque-texto-g__texto.py-5.px-4
        h4.mb-3 Ruta Turstica
        br
        br
        p.mb-0 Es el recorrido que se realiza en un territorio determinado bajo una propuesta estandarizada de lugares y actividades, con el fin de admirar los espacios naturales y disfrutar el legado cultural a partir de la interaccin entre grupos sociales que habitan el mismo territorio. Cuentan con un itinerario previo que, a su vez, facilita la orientacin del turista en el destino.
    p.mt-5(data-aos="fade-up-right") Dentro de una ruta turstica se podrn encontrar seales y carteles especiales que permiten resaltar los atractivos tursticos del lugar, con la intencin de facilitar el recorrido del turista. Estos carteles, por tanto, no solo definen la ubicacin, sino tambin la informacin aportante del atractivo a visitar.
    p.mb-5(data-aos="fade-up-right") En los ltimos aos, las rutas tursticas han tomado bastante importancia al momento de viajar ya que ayudan a evitar la saturacin de ciertos destinos, responden a las nuevas tendencias del mercado, y les brindan a los turistas experiencias innovadoras al tener contacto con diferentes comunidades autctonas y sus manifestaciones culturales.

    .row.justify-content-around.align-items-center.mt-3
      .col-xxl-10.col-xl-10.col-11.col-md-11(data-aos="fade-up-right")
        .tarjeta.fondo8.p-3.mb-5
          .row.justify-content-center.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/tema1/figura2.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p En Colombia hay cinco rutas tursticas que ofrece el Ministerio de Comercio y Fontur a nacionales y extranjeros y que permiten conocer el territorio, en el siguiente enlace se podr consultarlas:
                .col-sm-auto
                  a.boton.color-acento-botones.texto-blanco(href="https://fontur.com.co/es/eventos/estas-son-las-5-nuevas-rutas-turisticas-que-mincomercio-y-fontur-tienen-para-ti?q=es/eventos/estas-son-las-5-nuevas-rutas-turisticas-que-mincomercio-y-fontur-tienen-para-ti" target="_blank")
                    span Ingresar
                    i.fas.fa-file-download
    p.my-5(data-aos="fade-up-right") Las rutas tursticas se establecen por tipos, dependiendo de su finalidad, o su recorrido, en el siguiente recurso se ofrece esta diferenciacin:
    p.subrayar.mb-5(data-aos="fade-up-right") Segn su finalidad
    .row.justify-content-center.my-5
        .col-xxl-6.col-lg-6.col-md-11.col-sm-11.col-11.mt-4(data-aos="fade-up-right")
          LineaTiempoD.color-secundario
            p(numero="1" titulo="Temtica") Con una orientacin de excursin o conocimientos.
            p(numero="2" titulo="Senderismo") Con formas activas de moverse por el territorio.
            p(numero="3" titulo="Bienestar") Con el predominio del deporte y el descanso activo.
            p(numero="3" titulo="Combinadas") Son las rutas que combinan elementos de todas las rutas anteriores.
        .col-xxl-6.col-lg-6.col-md-11.col-sm-11.col-11(data-aos="fade-up-left")
          img.mt-4(src="@/assets/curso/tema1/imagen26.jpg")
    p.subrayar.mb-5(data-aos="fade-up-right") Segn tipo de recorrido
    .row.justify-content-center
      .col-xxl-5.col-xl-5.col-lg-5.col-md-9.col-sm-9.col-11.fondo35.p-4.me-3.mb-5(data-aos="fade-up-right")
        .row
          .col-xxl-4.col-xl-4.col-lg-4.col-md-4.col-sm-4.col-5
            img(src="@/assets/curso/tema1/figura6.png")
          .col-xxl-12.col-xl-12.col-lg-12
            h3.mt-3 Circulares
            p Son las que inician desde un punto de partida, haciendo paradas en lugares de inters por su atractivo natural o cultural, hasta volver al punto inicial donde comenz el recorrido.
      .col-xxl-5.col-xl-5.col-lg-5.col-md-9.col-sm-9.col-11.fondo36.p-4.me-3.mb-5(data-aos="fade-up-right")
        .row
          .col-xxl-4.col-xl-4.col-lg-4.col-md-4.col-sm-4.col-5
            img(src="@/assets/curso/tema1/figura7.png")
          .col-xxl-12.col-xl-12.col-lg-12
            h3.mt-3 Lineales
            p Van en lnea, de un punto a otro, y los destinos tursticos se encuentran ligados por medio de caractersticas en comn.
    .bloque-texto-g.fondonaranja.p-3.p-sm-4.p-md-5.my-5.otrobloque(data-aos="fade-up-right")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema1/imagen27.png')})`}"
      )
      .bloque-texto-g__texto.py-5.px-4
        h4.mb-3 Circuito turstico
        br
        br
        p.mb-0 Es un recorrido que inicia desde un punto emisor o receptor y cuenta con atractivos y facilidades a lo largo de su ruta. Se le conoce como circuito porque siempre se regresa al punto de partida inicial, sin pasar dos veces por un mismo sitio.
    p Los circuitos se deben organizar teniendo en cuenta las jerarquas de los atractivos, ya que estos son la base primordial para el disfrute y motivacin de turistas. Otro aspecto que se debe tener en cuenta al momento de planificar el circuito, es el pblico objetivo y sus intereses, ya que no ser lo mismo ofrecer un circuito extico y lleno de aventuras a un turista joven y explorador, que a un adulto mayor, que tal vez se interese ms por productos clsicos y regionales.
