      h2 1.14 Establecimientos de alojamiento y hospedaje
    .row.fondo30.ampliarambos.justify-content-center.px-5
      .col-xxl-6.col-xl-6.col-lg-7.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
        p.mb-4 Son los establecimientos de comercio que brindan el servicio de alojamiento turstico no permanente inferior a 30 das, con o sin alimentacin y servicios bsicos y/o complementarios, mediante contrato de hospedaje, y se clasifican en:
        ul.lista-ul--color
          li.otrocolorcheck 
            i.fas.fa-check
            | Segn el tamao, y est determinado por el nmero de habitaciones, depende del pas. Pueden ser establecimientos pequeos, medianos o grandes.
          li.otrocolorcheck
            i.fas.fa-check
            | Segn la explotacin comercial o modalidad: de acuerdo con la Norma Tcnica Sectorial Colombiana NTSH 006 del 2009, los establecimientos de alojamiento y hospedaje se clasifican segn la modalidad en la prestacin del servicio, de la siguiente forma:
          p.ms-4 Albergue-refugio-hostal, aparta hotel, apartamentos tursticos, alojamiento rural-<i>lodge</i>, hotel, posadas tursticas y recinto de campamento o <i>camping</i>.  
      .col-xxl-6.col-xl-6.col-lg-5.col-md-11.col-sm-11.col-11(data-aos="fade-up-left")
