    p.mt-5 En el siguiente recurso educativo se ofrece una amplia explicacin a este respecto:
    //-slider    
    .tarjeta.tarjeta--azul.p-4.my-5.fondo13
      SlyderA.pt-4.otroslide(data-aos="fade-up-right")(tipo="b")
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Hostal
            p Un concepto ms <i>low-cost</i>, con habitaciones individuales y compartidas. 
            p En muchos casos tienen los servicios comunes como la cocina y los baos. son reconocidas hasta con 3 estrellas.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/hostal.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Aparta hotel
            p Es un tipo de hotel, orientado a las largas estadas, y se diferencian de los hoteles tradicionales por el hecho de ofrecer apartamentos enteros, en vez de habitaciones; en ese sentido estn dotadas de instalaciones, equipamiento y servicios en condiciones de ocupacin inmediata, es decir, combinan la comodidad e independencia de un apartamento, con servicios que normalmente ofrece y disfrutas en un hotel.  No cuentan con carcter de residencia permanente, ya que son lugares de estadas transitorias para los turistas. Estn ubicados en lugares vacacionales. Su estructura e instalaciones permiten la conservacin, elaboracin y consumo de alimentos.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/apartahotel.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Alojamiento rural
            p Son casas de alquiler, a las afueras de la ciudad, cerca de las montaas, o de lugares similares; para escapadas de un par de das o fines de semana. Ideales para ir con la familia o un grupo de amigos. No cuentan con servicios personalizados.

          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/alojamientorural.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 <i>Camping</i>
            p Una alternativa a los clsicos y muy popular para quienes quieren escapar de lo cotidiano, se asocian con tiendas de campaa o carpas para pasar la noche. Se usan mucho en los safaris, expediciones, grupos de escuelas y universidades. En definitiva, para aquellos que quieren disfrutar de la naturaleza.
            p Aunque no est estipulado en la norma de turismo; actualmente existe un sub-titpo de <i>camping</i> pero con ms <i>glamour</i> y estilo y es el llamado: <i>Glamping</i>.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/camping.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 <i>Glamping</i>
            p La unin de las palabras <i>Glamour + Camping</i>, est adquiriendo relevancia en el mundo viajero, porque combina los beneficios de acampar al aire libre y cerca de la naturaleza (<i>camping</i>), con la exquisitez y comodidades que te ofrece un hotel de lujo (<i>glamour</i>).
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/glamping.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Hotel
            p Un hotel es el concepto ms comn y ms amplio que encontramos. Es bsicamente uno de los tipos de hospedaje que alberga de forma temporal a huspedes o viajeros en habitaciones independientes, brindndoles diversos servicios diarios (limpieza, comida, recepcin, etc.) personalizados y complementarios. Por lo general, se clasifican entre 1 y 7 estrellas.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/hotel.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 <i>Resort</i>
            p Hotel orientado hacia la recreacin.
            p Cuentan con varios restaurantes, piscinas. 
            p En ocasiones se llega a percibir como destinos todo incluido 
            p Cobra una sola tarifa Tienden a estar en categora de calidad o lujo.

          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/resort.png')
        .row.justify-content-center
          .col-lg-4.col-md-10.mb-4.mb-md-0
            h4 Hotel Casino
            p Hotel que tiene juegos de azar.
            p En algunos lugares (especialmente en Europa) los hoteles casinos son sofisticados y elegantes.
            p En otros lugares (especialmente en Las Vegas) son <i>resort</i> extravagantes.
          .col-lg-7.col-md-8
            figure
              img(src='@/assets/curso/tema1/casino.png')
    p.mb-5(data-aos="fade-up-right") Adems, se encuentra una clasificacin segn las estrellas otorgadas. Normalmente, los hoteles deben cumplir unos requisitos mnimos para tener diferentes estrellas que van desde la superficie mnima de las habitaciones, hasta el equipamiento con el que cuenta el hotel. En Colombia se han establecido diferentes requisitos que deben ser cumplidos por los hoteles que deseen certificar su calidad bajo la clasificacin por estrellas.

    //-slider
    SlyderF.my-5(data-aos="fade-up-right")(columnas="col-lg-6 col-xl-6")
      .tarjeta.otra2.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-4.col-md-5.col-8
            img(src='@/assets/curso/tema1/star1.png')
        h3.text-dark 1 Estrella
        p.text-dark Alojamiento limpio, bsico, puede o no tener un bao privado, telfono en la habitacin, televisin o servicios. 
        p Un hotel de una estrella es ideal para el viajero con poco presupuesto, donde el precio es la principal preocupacin.
        p Debern cumplir el 100% de los requisitos definidos en la Norma NTSH006, 178 puntos y los requisitos legales vigentes.
      .tarjeta.otra2.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-5.col-md-5.col-8
            img(src='@/assets/curso/tema1/star2.png')
        h3.text-dark 2 Estrellas
        p.text-dark Alojamiento limpio y bsico, pueden tener o no un centro de negocios o acceso a internet, pero por lo general sin salas de reuniones, botones, gimnasio o instalaciones recreativas. El restaurante a menudo se limita a servicio de caf o un desayuno continental. Las habitaciones ofrecen normalmente bao privado, telfono, televisin y servicios limitados.
        p Es ideal para viajeros de negocios o exploradores, donde el costo es un factor, pero s se desean servicios bsicos.
        p Debern cumplir el 100% de los requisitos definidos en la Norma NTSH006, 181 puntos y los requisitos legales vigentes.
      .tarjeta.otra2.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-5.col-md-4.col-8
            img(src='@/assets/curso/tema1/star3.png')
        h3.text-dark 3 Estrellas
        p.text-dark Mayor nfasis en la comodidad, estilo y servicio personalizado. A menudo tienen un restaurante, gimnasio, una tienda de souvenirs o elementos de higiene personal y botones. Generalmente, hay salas de juntas y / o salas de conferencia y servicios relacionados disponibles. Las habitaciones incluyen ms servicios y hay una atencin ms cuidadosa a la calidad y comodidad.
        p Es ideal para viajeros de negocios o de placer en busca de un poco ms que los servicios bsicos.
        p Debern cumplir el 80% de los requisitos definidos en la Norma NTSH006, rango 208 - 260 puntos y los requisitos legales vigentes.
      .tarjeta.otra2.p-4.fondo19
        .row.justify-content-left.mb-3
          .col-xxl-6.col-md-4.col-8
            img(src='@/assets/curso/tema1/star4.png')
        h3.text-dark 4 Estrellas
        p.text-dark Mayor calidad y mayor nivel de confort. Tienen ms de un restaurante, bar y servicio a cuartos con horario prolongado. Los servicios disponibles pueden incluir botones, limpieza y servicio de <i>valet parking</i>. Normalmente, cuentan con centro de conferencias y servicios de negocios. Las habitaciones son amplias y por lo general ofrecen un mobiliario elegante, ropa de cama de alta calidad, productos de bao y una amplia gama de servicios como minibar y secadora de pelo.
        p Debern cumplir mnimo con el 85% de los requisitos definidos en la Norma NTSH006, rango 283 - 333 puntos y los requisitos legales vigentes.
      .tarjeta.otra2.p-4.fondo19
        .row.justify-content-start.mb-3
          .col-xxl-6.col-md-5.col-8
            img(src='@/assets/curso/tema1/star5.png')
        h3.text-dark 5 Estrellas
        p.text-dark Muy orientado al servicio y brinda altos estndares de comodidad y calidad. Cuenta con ms de un restaurante, a menudo incluyendo una opcin de restaurante <i>gourmet</i>, as como un bar o sala de estar y servicio a cuartos las 24 horas. Puede ofrecer canchas de tenis y acceso a campo de golf, as como un <i>spa</i>, un gimnasio moderno. 
        p Debern cumplir mnimo con el 95% de los requisitos definidos en la norma NTSH006, Rango 349 - 337 puntos y los requisitos legales vigentes.
    p.my-5(data-aos="fade-up-right") Segn la explotacin comercial, modalidad o temtica escogida para llevar a cabo su oferta, los hoteles pueden especializarse en las siguientes:
    .row.justify-content-center
      .col-xxl-8.col-xl-8.col-lg-10.col-md-11.col-12.fondo2.mb-5.py-3(data-aos="fade-up-right")
        h3.text-white.text-center.pt-2.pb-3.mb-0 Clasificacin en funcin de su temtica, localizacin o rgimen de alojamiento
    //-slider    
    .tarjeta.tarjeta--azul.p-4.mb-5.fondo7
      SlyderA.pt-4.otroslide3(data-aos="fade-up-right")(tipo="b")
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Hoteles <i>Spa</i>
            p Un hotel <i>spa</i> es un tipo de alojamiento que cuenta entre sus instalaciones con servicios de tratamiento de salud con agua. 
            p Esto se traduce en hoteles con servicios con piscinas especiales y climatizadas, especiales para el <i>relax</i> en familia o con la pareja
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider31.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Hotel <i>Boutique</i>
            p Estos hoteles cuentan con pocas habitaciones, idealmente menos de 30, y en muchas ocasiones las caractersticas de espacio y decoracin distinguen claramente a cada una de ellas. Generalmente estn ambientados con una temtica o estilo particular. Los hoteles <i>boutique</i> van orientados al turismo de esparcimiento; ofrecen un servicio personalizado que en muchas ocasiones incluye servicio de masajes y <i>SPA</i>. La arquitectura, decoracin y mobiliario generalmente dan una personalidad distintiva al lugar. 
        
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider32.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Hoteles Hacienda
            p Estos hoteles de gran extensin se encuentran en sitios originalmente aislados o retirados de las ciudades. 
            p En algn momento los inmuebles fueron minas, fincas u otro tipo de centros de trabajo y ahora han sido remodelados, incorporndoseles extensas reas verdes y espacios para la recreacin y actividades al aire libre, sin perder su esencia histrica. 
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider33.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Hotel <i>Resort</i>
            p Son un complejo turstico con una calificacin de entre 4 y 5 estrellas.  Normalmente se encuentran ubicados en ambientes naturales o playas, lo cual permite ofrecer a sus huspedes una  cantidad de actividades de ocio adicionales. Cuentan con todos los servicios bsicos de los que dispone un hotel y muchos de ellos tienen regmenes de todo incluido que permiten que, por un precio fijo, se puedan  aprovechar todos los servicios que ofrece el complejo hotelero, as como actividades para nios y para adultos. Ejemplo: Hoteles Decamern
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider34.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Hoteles de Naturaleza
            p Estn situados en las proximidades de parajes naturales de inters como parques naturales, reservas y reas protegidas. Las estancias suelen ser de varios das. El turismo ecolgico es una de las actividades que esta creciendo por las variedades que la naturaleza y las costumbres que los habitantes nos brindan, aunque la naturaleza como la nica y verdadera fuente de descanso y paz, por lo tanto tenemos que ser muy responsables para no causar dao a la naturaleza ni a los nativos de la regin. Debido a su rpido crecimiento han contribuido al desarrollo de la actividad turstica. Ejemplo: Ecohabs Minca (Santa Marta, Colombia)
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider35.png')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Hoteles temticos para nios y adultos
            p Estos hoteles tienen la singularidad de corresponderse con una idea general. As pues, podemos ver hoteles temticos inspirados en lugares especiales como en decorados de cine, caracterizados con personajes de pelculas famosas, o diseados en los entornos ms sorprendentes. 
            p Ejemplo: hoteles de <i>Disney</i>
          .col-md-7
            figure
              img(src='@/assets/curso/tema1/slider36.png')
        .row.justify-content-center
          .col-md-11
            figure
              img(src='@/assets/curso/tema1/slider37.png')
        .row.justify-content-center
          .col-md-11
            figure
              img(src='@/assets/curso/tema1/slider38.png')
