    #sostenibilidad.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.15 Sostenibilidad turstica
    .row.justify-content-center
      .col-xxl-6.col-xl-6.col-lg-7.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
        p El turismo sostenible est enfocado en generar un menor impacto o desgaste en la cultura del destino y en el medioambiente. Se le ha reiterado su importancia desde los aos noventa, donde se comenz a evidenciar los efectos adversos de la actividad no solo en el medio ambiente, sino tambin en la cultura local de los destinos. La sostenibilidad, por tanto, busca satisfacer las necesidades de los turistas, pero teniendo en cuenta las consecuencias actuales y futuras, econmicas, sociales y medioambientales que puede haber en la industria, el entorno y de las comunidades anfitrionas.  
        p Es importante comprender que no es un subtipo, ni una clase, ni una forma diferente de turismo; por el contrario, todos los tipos de turismo deberan ser conscientes con el medioambiente y la cultura del territorio. Ejerciendo la actividad turstica de manera sostenible, no solo se busca mitigar los impactos negativos de la industria, sino tambin buscar beneficios para las comunidades locales a nivel econmico y social a partir de la sensibilizacin y el apoyo para la conservacin del entorno. 

      .col-xxl-6.col-xl-6.col-lg-5.col-md-9.col-sm-11.col-11(data-aos="fade-up-left")
        img(src='@/assets/curso/tema1/imagen29.jpg')
    p.my-5(data-aos="fade-up-right") Segn la explotacin comercial, modalidad o temtica escogida para llevar a cabo su oferta, los hoteles pueden especializarse en las siguientes:
    .row.justify-content-around(data-aos="fade-up-right")
      .col-xxl-4.col-xl-4.col-lg-4.col-md-6.col-sm-11.col-11.fondo37.p-5.mb-5
        h4.mb-4 Dimensin sociocultural
        p Hace referencia al respeto por la identidad de las comunidades anfitrionas, su cultura, valores y tradiciones. Tambin a la preservacin de los activos culturales, arquitectnicos y arqueolgicos de los destinos, y a la proteccin del patrimonio inmaterial y las costumbres ancestrales de comunidades tnicas (Poltica de Turismo Sostenible, MINCIT 2012).
      .col-xxl-4.col-xl-4.col-lg-4.col-md-6.col-sm-11.col-11.fondo38.p-5.mx-1.mb-5
        h4.mb-4 Dimensin ambiental 
        p Gestin ptima de los recursos naturales que estn en la base del desarrollo turstico, y a la conservacin del medio ambiente y de los ecosistemas, que soportan la vida de distintas especies y la salud del ser humano.
      .col-xxl-4.col-xl-4.col-lg-4.col-md-6.col-sm-11.col-11.fondo39.p-5.mb-5
        h4.mb-4 Dimensin econmica
        p El turismo sostenible debe ser rentable y debe haber una distribucin equitativa de los beneficios socioeconmicos como el empleo estable y la obtencin de ingresos y servicios sociales para las comunidades anfitrionas.
    .row(data-aos="fade-up-right")
      .col-xxl-5.col-xl-5.col-lg-6.col-md-9.col-sm-12.col-12.fondomundo3.ps-4
        h4.ps-5.pt-5.ms-5.mt-3 Colombia y el turismo sostenible
      p.my-5 El compromiso de Colombia con la sostenibilidad inici en el ao 2012, cuando el pas defini los ODS (Objetivos de Desarrollo Sostenible) en la Cumbre de Ro de Janeiro. Este evento estuvo enfocado en los requerimientos econmicos e institucionales para el desarrollo sostenible y all Colombia defendi la necesidad de realizar prcticas sostenibles cuando de turismo se habla y adicionalmente mencion que los ODS deberan ser aplicados en todos los pases y no solo en las economas emergentes a las que estaban inicialmente dirigidos (ver figura 13). 
    .row.justify-content-center.mb-5(data-aos="fade-up-right")
      .col-xxl-10.col-xl-10.col-lg-10.col-md-11.col-sm-11.col-11
        .cajon.puntacajon.p-4.mb-4.fondo17.sombracajon
          p.mb-0.mx-3 En el documento plasmado por la Organizacin Mundial del Turismo y la Organizacin de los Estados Americanos en el 2018 y titulado El turismo y los Objetivos de Desarrollo Sostenible - Buenas prcticas en las Amricas se explican 17 objetivos creados con el fin de erradicar la pobreza, proteger el planeta y garantizar la igualdad y la prosperidad para todos. En dichos objetivos se le presta gran importancia al rol que pueden desempear las empresas para crear un desarrollo ms sostenible para todos. 

    .titulo-sexto.color-acento-contenido.my-5(data-aos="fade-up-right")
      span.text-bold.mb-0 Figura 13. 
      span Objetivos de Desarrollo Sostenible
    .row.justify-content-center.mb-3(data-aos="fade-up-right")
      .col-xxl-8.col-xl-8.col-lg-8.col-md-11.col-sm-11.col-11
        img(src='@/assets/curso/tema1/imagen30.png')
    p.text-center.mb-0(data-aos="fade-up-right") Nota. Tomado de 
    p.text-center.mb-5(data-aos="fade-up-right") https://www.cepal.org/sites/default/files/styles/content_big/public/static/images/e_2018_ods_poster_with_un_emblem_es.png?itok=aSBP-hQC
    p.mt-5(data-aos="fade-up-right") En el 2012, en Colombia se genera una poltica de naturaleza, y su objetivo es posicionar a Colombia como destino de turismo de naturaleza, reconocido por el desarrollo de productos y servicios altamente competitivos y sostenibles.
    p.mb-5(data-aos="fade-up-right") Desde el Ministerio de Industria, Comercio y Turismo de Colombia tambin se ha implementado el programa de Normas Tcnicas Sectoriales de Turismo Sostenible, que buscan asegurar mayores estndares de calidad en los servicios de las empresas tursticas, certificando as que sus actividades fomentan el turismo sostenible y la proteccin de la biodiversidad de la zona. 
    .row.justify-content-around.align-items-center.mt-3
      .col-xxl-10.col-xl-10.col-11.col-md-11(data-aos="fade-up-right")
        .tarjeta.fondo8.p-3.mb-5
          .row.justify-content-center.align-items-center
            .col-3.col-sm-2.col-lg-1
              img(src="@/assets/curso/tema1/figura2.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p Las Naciones Unidas y la Organizacin Mundial del Turismo, han formulado el Cdigo tico Mundial para el Turismo, para llevar a cabo una prctica responsable y sostenible de gobiernos, empresas, comunidades y turistas, con responsabilidad en cada uno de sus territorios, en el siguiente enlace podr consultarlo:
                .col-sm-auto
