    #colombia.titulo-segundo.color-acento-contenido(data-aos="fade-down-right")
      h2 1.16 Colombia turstica
    p.mb-5(data-aos="fade-up-right") Colombia tiene un amplio territorio, surcado por 3 cordilleras lo que permite disfrutar de varios pisos trmicos, variedad de cultivos y bosques tropicales, a continuacin, se podr aprender un poco ms de las bellezas naturales con que se cuenta:
    figure.mb-5(data-aos="fade-up")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/DyHw5c5pHiE" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    p.mt-5(data-aos="fade-up-right") En Colombia no existen estaciones (invierno, verano, primavera, otoo) como en otros pases el mundo, esto debido a su ubicacin en la zona ecuatorial que mantiene una igualdad en las horas en las que sale el sol o cae la noche. A cambio de estaciones, en el pas se puede hablar de temporadas secas (de diciembre a enero y de julio a agosto) y temporadas de lluvias (de abril a mayo y de octubre a noviembre). Adicional a lo anterior, en Colombia tambin se puede encontrar una gran variedad de paisajes, biodiversidad y de pisos trmicos debido a la diversidad topogrfica del territorio. 
    p.mb-5(data-aos="fade-up-right") El clima en el pas vara de acuerdo con los pisos trmicos que hay en cada regin, de acuerdo con la explicacin que encuentra a continuacin:
    .row.justify-content-center
      .col-xxl-6.col-xl-6.col-lg-7.col-md-11.col-sm-11.col-11(data-aos="fade-up-right")
        LineaTiempoD.color-secundario
          p(numero="1" titulo="Clima clido") Son zonas en las que el calor supera los 24 C., como ocurre en el Pacfico, en donde se puede hacer avistamiento de ballenas jorobadas; San Andrs, un destino con playas increbles para bucear; y  la costa Caribe, en donde se celebra el famoso Carnaval de Barranquilla, y en donde se encuentra el Parque Nacional Natural Tayrona.
          p(numero="2" titulo="Clima templado") En estas zonas, la temperatura oscila entre los 17C. Y los 24 C. Como ocurre en Medelln, en donde se celebra la Feria de las flores, o en Cali hogar de la famosa Feria de Cali.
          p(numero="3" titulo="Clima fro") La temperatura oscila entre los 12 C y los 17 C. Ejemplo de ste clima es la ciudad de Bogot, la cual fusiona historia, patrimonio y modernidad, en donde hay variedad de museos y centros culturales, como el Museo del Oro del Banco de la Repblica.
          p(numero="4" titulo="Pramos") En estas zonas, las temperaturas oscilan entre los 6 C y los 12 C. El parque Nacional Natural Chingaza, en el cual habitan especies como venados de cola blanca, cndores y osos andinos.
          p(numero="5" titulo="Zonas glaciares") Tienen una temperatura inferior a los 6 C. En la Sierra Nevada de Santa Marta, se puede apreciar paisajes nevados, adems de practicar el <i>trekking</i>.
          p(numero="6" titulo="Selvas tropicales") Son hbitats calurosos y hmedos, y las temperaturas superan los 27 C. En Colombia, la regin del Amazonas es una selva tropical, con gran riqueza de paisajes naturales. Hbitats calurosos y hmedos, y las temperaturas superan los 27 C. En Colombia, la regin del Amazonas es una selva tropical, con gran riqueza de paisajes naturales.
          p(numero="7" titulo="Tropical de estepa") Se caracteriza por sus altas temperaturas, sus lluvias mnimas y muy poca vegetacin, con temperaturas que sobrepasan los 34 C. La Guajira es un ejemplo de este clima, en donde se puede disfrutar de paisajes desrticos, visitar las rancheras y conocer algo ms de la cultura <i>Wayuu</i>.
      .col-xxl-6.col-xl-6.col-lg-5.col-md-8.col-sm-11.col-11.my-5(data-aos="fade-up-right")
        img(src='@/assets/curso/tema1/imagen31.jpg')
    p.mt-5(data-aos="fade-up-right") Anteriormente por parte del turismo, Colombia no obtena ingresos importantes debido a la inseguridad y la violencia en la que se haba sumergido desde los aos sesenta; sin embargo, este panorama cambi y en la ltima dcada bajo todas las polticas pblicas, el pas ha tenido un desarrollo econmico y cultural exponencial, lo que ha ayudado a que el sector turismo ocupe una de las bases fundamentales del pas.
    p.mb-5(data-aos="fade-up-right") Colombia est lleno de atractivos tursticos, su variada geografa, la riqueza ecolgica, la variedad de regiones y climas, su historia llena aventuras, sus personajes representativos, sus residentes y su cultura, ha encantado a propios y forneos durante aos. Es un pas que hace vibrar a sus visitantes, con sus emociones y toda su cultura. El gobierno ha enfatizado 7 sectores de turismo, como forma de promocin, estos son (ver figura 14):
    .titulo-sexto.color-acento-contenido.my-5(data-aos="fade-up-right")
