        p En la siguiente infografa se podr conocer ms acerca de estas fuentes de informacin estadstica y su uso:
      .col-xxl-8.col-xl-8.col-lg-8.col-md-11.col-11(data-aos="fade-up-left")
        img(src="@/assets/curso/tema1/imagen33.jpg")

    .row.justify-content-center.my-5
      .col-xxl-6.col-xl-6.col-lg-6.col-md-8.col-8(data-aos="fade-up-right")
        img(src="@/assets/curso/tema1/imagen34.svg")
      .col-xxl-6.col-xl-6.col-lg-6.col-md-11.col-11(data-aos="fade-up-left")
        //-slider    
        .tarjeta.tarjeta--gris.px-4.my-4.pt-4
          SlyderA.otroslide2(tipo="b")
            .row.justify-content-center
              .col-lg-4.col-md-4.col-5
                figure
                  img(src='@/assets/curso/tema1/citur.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 Sistema de estadsticas de turismo que permite identificar la estructura y evolucin del turismo a partir de un conjunto de datos sobre la oferta y demanda a nivel nacional e internacional. 
                p Este sistema mide varios aspectos:
                p Estadstica econmico, empleo, flujos tursticos, flujos areos, parques nacionales naturales, agencias de viaje, hoteles y transporte terrestre.
            .row.justify-content-center
              .col-lg-5.col-md-5.col-5
                figure
                  img(src='@/assets/curso/tema1/situr.svg')
              .col-lg-12.col-md-12.mb-md-0.px-4
                p.mt-5 Es un sistema de medicin, similar al anterior, pero en este caso es utilizado para cada departamento. En su pgina <i>web</i> se pueden seleccionar los diferentes departamentos con sus municipios, para encontrar informacin turstica asociada a los atractivos tursticos del lugar, indicadores departamentos, indicadores de sostenibilidad y mapa regional de oportunidades. 
            .row.justify-content-center
              .col-lg-6.col-md-5.col-6.col-sm-6
                figure
                  img(src='@/assets/curso/tema1/dane.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 El DANE (entidad responsable de la planeacin, levantamiento, procesamiento, anlisis y difusin de las estadsticas oficiales de Colombia), como lo es la Encuesta de Gasto en Turismo Interno (EGIT) y de la Encuesta Nacional de Hoteles, cuyo objetivo es consolidar el sistema de estadsticas de turismo con el fin de disponer  de informacin que permita el diseo eficiente de polticas, estrategias, planes y programas tursticos y fortalecer el proyecto de cuenta satlite de turismo de Colombia.
            .row.justify-content-center
              .col-lg-4.col-md-4.col-5
                figure
                  img(src='@/assets/curso/tema1/anato.svg')
              .col-lg-12.col-md-12.mb-4.mb-md-0.px-4
                p.mt-5 ANATO (Asociacin Nacional de Agencias de Viajes y Turismo) viene trabajando con sus Agencias Asociadas para recolectar los datos en las Encuestas de Temporada, las cuales presentan datos asociados a las ventas de las Agencias en las cuatro temporadas altas del ao y en la actualidad, mediante la Encuesta Trimestral que mide los resultados de las Agencias para cada trimestre en especfico.
    p.my-5(data-aos="fade-up-right") El Ministerio de Comercio, Industria y Turismo de Colombia, en su plataforma Centro de Informacin Turstica de Colombia, CITUR, establece la medicin de una serie de datos, que permiten obtener estadsticas puntuales, estos son (ver tabla 6):
