    <table>
      <tr style="align-items: center;">
        <th class="fondo6 text-center anchocol"><h4 class="text-bold">Estadstica econmica</h4></th>
        <td style="background-color: #F6F6F6; align-items: center;">
          <span>Muestra un panorama cualitativo y cuantitativo del volumen, composicin y dinamismo de las fuerzas productivas de un pas.</span><br/>
          <span>Registro Nacional de Turismo: La Ley 300 de 1996 en el artculo 61 estableci el Registro Nacional de Turismo y el artculo 12 de la Ley 1101 de 2006 define los prestadores de servicios tursticos obligados a registrarse antes de iniciar sus operaciones en Colombia. Vienen clasificados por categora y subcategora segn sea el caso.</span><br/>
          <span>Este Registro es obligatorio para su funcionamiento y debe actualizarse anualmente.</span><br/>
          <span>En esta seccin se miden el nmero de Prestadores de Servicios Tursticos en Colombia, nmero de habitaciones, camas y empleados de los prestadores de servicios tursticos de alojamiento, nmero de empleados de los prestadores de servicios tursticos, etc.</span>
        </td>
      </tr>
      <tr>
        <th class="fondo6 text-center anchocol"><h4 class="text-bold">Empleo</h4></th>
        <td style="background-color: #F6F6F6;">
          <p style="margin-bottom: 0px;">Los indicadores de mercado laboral muestran las condiciones y caractersticas de empleo de los colombianos.</p>
        </td>
      </tr>
      <tr>
        <th class="fondo6 text-center anchocol"><h4 class="text-bold">Flujos tursticos</h4></th>
        <td style="background-color: #F6F6F6;">
          <p style="margin-bottom: 0px;">Corresponde a corrientes de turistas que se desplazan a nivel mundial con el objetivo de tener nuevas experiencias en diferentes mbitos de la vida como ocio, convenciones, salud, cultura, entre otras. Ac se mide la llegada de cruceros, turismo emisor y turismo receptor.</p>
        </td>
      </tr>
      <tr>
        <th class="fondo6 text-center anchocol"><h4 class="text-bold">Flujos areos</h4></th>
        <td style="background-color: #F6F6F6;">
          <p style="margin-bottom: 0px;">Miden las llegadas de pasajeros areos nacionales e internacionales en vuelos regulares.</p>
